import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner, Box } from "@chakra-ui/react";
import Toast from "./toast";



export default function Downlines({ setPageTitle, setSelected, userData }) {
    useEffect(() => {
        setSelected(5)
        setPageTitle("My Downlines")
    }, [])
    return (
        <Flex w="100%" direction="column">
            <Flex w="100%" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" bg="#fff" mb="7">
                <Flex py="5" px="6" color="#575962" fontSize="20px" align="center">Referral link: <Text color="#f25961" fontSize="13px" cursor="pointer" _hover={{ color: "#8b0000" }} transition="300ms ease-in-out" ml="2" mt="1" onClick={() => {
                    navigator.clipboard.writeText(userData[0]?.uname)
                    Toast("Link copied to  your clipboard!", "success")
                }}>/register/{userData[0]?.uname}</Text></Flex>
            </Flex>

            <Flex w="100%" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3" bg="#fff">
                <Flex py="5" px="6" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">My Downlines</Flex>
                
                <Box w={["310px", "100%"]} overflow={["auto", "hidden"]}>
                    <Flex py="5" px="6" direction="column" minWidth={["600px", "100%"]}>
                        <Flex w="100%">
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Name</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Username</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Level</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Amount Earned</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Investment</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Date Registered</Text>
                        </Flex>
                    </Flex>
                </Box>
            </Flex>
        </Flex>
    )
}