import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner } from "@chakra-ui/react";
import Bell from "./bell.png";


export default function Notifications({ setPageTitle, setSelected, userData }) {
    const [showMsg, setShowMsg] = useState(0)
    useEffect(() => {
        setSelected(9)
        setPageTitle("Messages")
    }, [])
    const msgs = [1, 2]

    return (
        <Flex w="100%" justify="space-between" direction={["column", "row"]}>
            <Flex w={["100%", "35%"]} boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3" bg="#fff">
                <Flex py="5" px="6" fontSize="20px" borderBottom="1px solid #dee2e6" align="center" color="#0B51AC"><i className="mdi mdi-bell" style={{ fontSize: "30px" }}></i> Notifications</Flex>
                <Flex py="5" px="3" direction="column">
                    {
                        msgs.map((item, index) => (
                            <Flex w="100%" key={index} onClick={() => {
                                setShowMsg(item)
                            }} borderBottom="1px solid #dee2e6" pb="4" cursor="pointer" mb="4" align="center">
                                <Image w="40px" h="42px" src={Bell} />
                                <Flex flex="1" ml="3" direction="column">
                                    <Text color="#1572e8" fontWeight="bold" fontSize="14px">ACCOUNT STATUS</Text>
                                    <Text fontSize="11px" fontStyle="italic" color="#999">Message from admin</Text>
                                </Flex>
                            </Flex>
                        ))
                    }
                </Flex>
            </Flex> 

            <Flex w={["100%", "62%"]} boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3" bg="#fff" colot="#575962" px="4">
                {
                    showMsg === 0 ?
                    <Flex pt="7" pb="6" px="8" direction="column" align="center">
                        <Flex mt="-2" w="100%" bg="#fff" borderLeft="4px solid #48abf7" padding=".95rem 1.25rem" boxShadow="1px 1px 14px 0 rgb(18 38 63 / 26%)" fontSize="14px">Select a notification</Flex>
                    </Flex>
                    :
                    showMsg === 1 ?
                    <>
                        <Flex py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6" justify="center">ACCOUNT STATUS</Flex>
                        <Flex py="5" px="6" direction="column" fontSize="14px" color="#575962">
                            <Text>Hello <Text mr="1" as="span" textTransform="capitalize">{userData[0]?.uname},</Text>
                           Kindly update your profile</Text>
                        </Flex>
                    </>
                    :
                    <>
                        <Flex py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6" justify="center">ACCOUNT STATUS</Flex>
                        <Flex py="5" px="6" direction="column" fontSize="14px">
                            <Text mb="4" color="#575962">Hello <Text as="span" textTransform="capitalize">{userData[0]?.uname},</Text></Text>
                            <Text mb="4" color="#575962">How are you doing today? We are sending this message to you to inform you as regards your account completion. Kindly message the support live chat for further inquiries.</Text>
                            <Text color="#575962">Thanks, ADMIN.</Text>
                        </Flex>
                    </>
                }
            </Flex> 
        </Flex>
    )
}