import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner } from "@chakra-ui/react";
import Logo from "./logo.png";
import { useHistory } from "react-router-dom";


function ResetPassword() {
	useEffect(() => {
		document.title = "Password Recovery - Best Cryptocurrency Investment Platform"
	}, [])
	async function resetFunc(e) {
		e.preventDefault()
	}
	const history = useHistory()
    return(
		<Flex w="100%" bg="#fff" color="#495057" minHeight="100vh" fontFamily="'Lato', sans-serif">
            <Flex bg="rgba(20, 20, 100, .8)" w="100%" minHeight="100vh" justify="center" align="center" direction="column">
                <Flex w={["90%", "34%"]} bg="#fff" borderRadius="5px" px="8" py="6" direction="column">
                    <Flex w="100%" direction="column" align="center">
                        <Image src={Logo} h="70px" />
                        <Flex align="center" color="#575962">
                            <i className="mdi mdi-key-variant" style={{ fontSize: "25px" }}></i>
                            <Text fontSize="1.1625rem" ml="1">Password Recovery</Text>
                        </Flex>
                    </Flex>
                    
			        <Flex w="100%" bg="rgba(0,0,0,.1)" mt="5" mb="8" h="1px"></Flex>
                    <form onSubmit={(e) => resetFunc(e)}>
                        <Text color="#495057" mb="2" fontSize="1rem">Email Address</Text>
                        <input type="email" style={{ width: "100%", border: "1px solid #ced4da", padding: "10px", color: "#495057", borderRadius: ".25rem" }} required className="fInput" />

                        <Flex w="100%" mt="5" direction="column">
						    <Flex><button type="submit" style={{ background: "transparent", border: "none" }}><Flex cursor="pointer" bg="#1572e8" color="#fff" fontSize="14px" borderRadius="3px" padding=".65rem 1.4rem" _hover={{ opacity: "0.9" }} transition="300ms ease-in-out">Reset Password</Flex></button></Flex>
                            
						    <Flex mb="6"><Flex cursor="pointer" mt="7" color="#007bff" fontSize="14px" onClick={() => history.push("/login")} align="center"><i className="mdi mdi-arrow-left" style={{ marginRight: "2px", fontSize: "18px" }}></i>Back to Login</Flex></Flex>
                        </Flex>
                    </form>


                </Flex>
            </Flex>
        </Flex>
    )
}

export default ResetPassword;