import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner, Box } from "@chakra-ui/react";
import pay1 from "./pay1.png";
import pay2 from "./pay2.png";
import pay3 from "./pay3.png";
import pay4 from "./pay4.png";
import Toast from "./toast";


export default function Wallet({ setPageTitle, setSelected, dData }) {
    useEffect(() => {
        setSelected(2)
        setPageTitle("Wallet")
    }, [])
    const pays = [
        {
            tag: "Deposit with Payeer gateway",
            icon: pay1,
            caption: "Pay with Payeer",
        },
        {
            tag: "Pay using Bitcoin (Coinpayment system)",
            icon: pay2,
            caption: "Pay with BTC",
        },
        {
            tag: "Pay using Ethereum (Coinpayment system)",
            icon: pay3,
            caption: "Pay with ETH",
        },
        {
            tag: "Pay using Bank Deposit/Transfer",
            icon: pay4,
            caption: "Deposit with Bank",
        },
    ]
    return (
        <Flex w="100%" direction="column">
           <Flex w="100%" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3" bg="#fff" mb="7">
                <Flex py="5" px="6" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Deposit into your wallet</Flex>
                <Flex pt="12" px="6" justify="space-between" flexWrap="wrap" w="100%" direction={["column", "row"]}>
                    {
                        pays.map((item, index) => (
                            <Flex w={["100%", "48%"]} px="4" direction="column" align="center" bg="#fff" key={index} border="1px solid #ccc" mb="7" borderRadius="7px" py="12" _hover={{ boxShadow: "-1px 1px 5px 3px #ccc" }} transition="300ms ease-in-out">
                                <Image  src={item.icon} h="55px" />
                                <Text textAlign="center" fontSize="14px" color="#575962" mt="5" mb="5">{item.tag}</Text>
                                <button style={{ background: "transparent", border: "none" }} onClick={() => {
                                    Toast("Communicate with our admin to continue with deposit processes", "success")   
                                    if(window.tidioChatApi) {
                                        setTimeout(function(){
                                            window.tidioChatApi.open()
                                        }, 1000)
                                    }
                                }}><Flex bg="#1269DB" cursor="pointer" padding="6px 15px" fontSize="14px" borderRadius="3px" color="#fff" transition="300ms ease-in-out" _hover={{ opacity: "0.8" }} mr="1">{item.caption}</Flex></button>
                            </Flex>
                        ))
                    }
                </Flex>
            </Flex>

            <Flex w="100%" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3" bg="#fff">
                <Flex py="5" px="6" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Deposit History</Flex>

                <Box w={["310px", "100%"]} overflow={["auto", "hidden"]}>
                    <Flex py="5" px="6" direction="column" minWidth={["900px", "100%"]}>
                        <Flex w="100%">
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Amount</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Method</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Account</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Acc Name</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Date </Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="16.6%">Status</Text>
                        </Flex>
                        {
                            dData[0]?.map((item, index) => (
                                <Flex w="100%" key={index} mb="2">
                                    <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="16.6%">${parseInt(item?.amount)?.toLocaleString()}</Text>
                                    <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="16.6%">{item?.method}</Text>
                                    <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="16.6%">{item?.acc}</Text>
                                    <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="16.6%">{item?.accname}</Text>
                                    <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="16.6%">{item?.transdate}</Text>
                                    <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="16.6%">{item?.status}</Text>
                                </Flex>
                            ))
                        }
                    </Flex>
                </Box>
            </Flex>
        </Flex>
 
    )
}