import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner } from "@chakra-ui/react";
import Logo from "./logo.png";
import { useHistory } from "react-router-dom";
import { ApiRequest } from "./request";
import Toast from "./toast";

function Signup() {
	const [loading, setLoading] = useState(false)
	useEffect(() => {
		document.title = "worldinvestbitcoin - Best Cryptocurrency Investment Platform"
	}, [])
	async function registerFunc(e) {
		e.preventDefault()
		var fname = document.getElementById('fname').value
		var lname = document.getElementById('lname').value
		var email = document.getElementById('email').value
		var uname = document.getElementById('uname').value
		var pass = document.getElementById('pass').value
		var cpass = document.getElementById('cpass').value
		if(pass === cpass) {
			setLoading(true)
			const res = await ApiRequest("post", "register.php", {
				fname: fname,
				lname: lname,
				email: email,
				uname: uname,
				pass: pass,
			})
			if(res?.status === true) {
				Toast(res.message, "success")
				sessionStorage.setItem("registerModal", "true")
				window.location.href = "/login"
			}
			else {
				Toast(res?.message, "error")
			}
			setLoading(false)
		}
		else {
			Toast("Password don't match", "error")
		}
	}
	const history = useHistory()
	return (
		<Flex w="100%" bg="#fff" color="#495057" fontFamily="'Lato', sans-serif">
			<Flex minHeight="100vh" w="50%" className="bg4" display={["none", "flex"]}>
				<Flex bg="rgba(20, 20, 100, .8)" w="100%" minHeight="100%" justify="center" align="center" direction="column" color="#fff">
					<Image src={Logo} h="70px" mt="-10%" />
					<Text fontSize="1.725rem" fontWeight="bold">worldinvestbitcoin</Text>
					<Text fontSize="1.0375rem" mt="5">Best Cryptocurrency Investment Platform</Text>
				</Flex>
			</Flex>
			<Flex minHeight="100vh" w={["100%", "50%"]} bg="#fff" direction="column" px="8" pb="6" pt={["12", "6"]}>
				<Flex w="100%" direction="column" align="center">
					<Image src={Logo} h="70px" />
					<Flex align="center" color="#575962">
						<i className="mdi mdi-account" style={{ fontSize: "25px" }}></i>
						<Text fontSize="1.1625rem" ml="1">Create an Account</Text>
					</Flex>
				</Flex>
				<Flex w="100%" bg="rgba(0,0,0,.1)" mt="3" mb="8" h="1px"></Flex>
				<form onSubmit={(e) => registerFunc(e)}>
					<Flex w="100%" justify="space-between" mb="5" direction={["column", "row"]}>
						<Flex w={["100%", "47.5%"]} mb={["5", "0"]}><input type="text" placeholder="First Name" style={{ width: "100%", border: "1px solid #CCF", padding: "10px", color: "#495057" }} required id="fname" /></Flex>
						<Flex w={["100%", "47.5%"]}><input type="text" placeholder="Last Name" style={{ width: "100%", border: "1px solid #CCF", padding: "10px", color: "#495057" }} required id="lname" /></Flex>
					</Flex>
					<Flex w="100%" mb="5"><input type="email" placeholder="Email Address" style={{ width: "100%", border: "1px solid #CCF", padding: "10px", color: "#495057" }} required id="email" /></Flex>

					<Flex w="100%" mb="5"><input type="text" placeholder="Username" style={{ width: "100%", border: "1px solid #CCF", padding: "10px", color: "#495057" }} required id="uname" /></Flex>

					<Flex w="100%" justify="space-between" mb="5" direction={["column", "row"]}>
						<Flex w={["100%", "47.5%"]} mb={["5", "0"]}><input type="password" placeholder="Password" style={{ width: "100%", border: "1px solid #CCF", padding: "10px", color: "#495057" }} required id="pass" /></Flex>
						<Flex w={["100%", "47.5%"]}><input type="password" placeholder="Confrim Password" style={{ width: "100%", border: "1px solid #CCF", padding: "10px", color: "#495057" }} required id="cpass" /></Flex>
					</Flex>

					<Flex w="100%" justify="center" mt="5" direction="column" align="center">
						<button type="submit" style={{ background: "transparent", border: "none" }}><Flex cursor="pointer" bg="#1572e8" color="#fff" fontSize="14px" borderRadius="3px" padding=".65rem 1.4rem" _hover={{ opacity: "0.9" }} transition="300ms ease-in-out">{loading ? <Spinner color="#fff" emptyColor="lightgrey" /> : "Regsiter"}</Flex></button>
						
						<Flex mt="5">Already have an account?<Text ml="2" cursor="pointer" color="#1572e8" fontWeight="bold" onClick={() => history.push("/login")}>Login</Text></Flex>
					</Flex>
				</form>
			</Flex>
		</Flex>
	)
}


export default Signup;