import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner } from "@chakra-ui/react";
import Logo from "./logo.png";
import { useHistory } from "react-router-dom";
import img10 from "./img10.png";
import mimg1 from "./mimg1.png";


function Footer({ selected, setSelected }) {
    const history = useHistory()
    const links = [
        {
            name: "HOME",
            link: "/index",
        },
        {
            name: "CONTACT US",
            link: "/contact-us",
        },
        {
            name: "ABOUT US",
            link: "/about-us",
        },
    ]
    const lnks = links.map((item, index) => (
        <Flex key={index} fontSize="13px" color="#C3C3C3" align="center" cursor="pointer" onClick={() => {
            index === 0 ? setSelected(0) : index === 1 ? setSelected(4) : setSelected(1)
            history.push(item.link)
        }} mb="3">
            <Flex w="5px" h="5px" borderRadius="5px" border="1px solid #FF9600" mr="2"></Flex>
            {item.name}
        </Flex>
    ))
    return (
        <>
            <Flex w="100%"><Image w="100%" src={img10} onClick={() => history.push("/register")} display={["none", "flex"]} /><Image w="100%" src={mimg1} onClick={() => history.push("/register")} display={["block", "none"]} /></Flex>

            <Flex w="100%" bg="#1A1A1A" px={["5", "12"]} py="6" direction="column">
                <Flex w="100%" direction={["column", "row"]}>
                    <Flex flex="2" direction="column">
                        <Text fontSize="20px" fontWeight="bold" color="#FF9600">OUR <Text display={["none", "block"]}></Text>COMPANY</Text>
                        <Text mt="5">{lnks}</Text>
                    </Flex>
                    <Flex flex="2" direction="column" mt={["5", "0"]}>
                        <Text fontSize="20px" fontWeight="bold" color="#FF9600">HELP &amp; <Text display={["none", "block"]}></Text> SUPPORT</Text>
                        <Text mt="5">{lnks}</Text>
                    </Flex>
                    <Flex flex="2" direction="column" mt={["5", "0"]}>
                        <Text fontSize="20px" fontWeight="bold" color="#FF9600">CONTACT US</Text>
                        <Text w="40px" h="2px" bg="#BBB" my="3"></Text>
                        <Flex align="center" fontSize="13px" color="#C3C3C3" cursor="pointer" onClick={() => window.open("mailto: info@worldinvestbitcoin.com")} mb="2">
                            <i className="mdi mdi-email" style={{ fontSize: "18px", color: "#FF9600" }}></i>
                            <Text ml="2">info@worldinvestbitcoin.com</Text>
                        </Flex>
                        
                        <Flex align="center" fontSize="13px" color="#C3C3C3" cursor="pointer" onClick={() => window.open("tel: +1 (740) 312-7338")} mb="2">
                            <i className="mdi mdi-phone" style={{ fontSize: "18px", color: "#FF9600" }}></i>
                            <Text ml="2">+1 (740) 312-7338</Text>
                        </Flex>
                        
                        <Flex align="center" fontSize="13px" color="#C3C3C3" mb="2">
                            <i className="mdi mdi-map-marker" style={{ fontSize: "18px", color: "#FF9600" }}></i>
                            <Text ml="2"></Text>
                        </Flex>
                        
                        <Flex align="center" fontSize="13px" color="#C3C3C3">
                            <i className="mdi mdi-lan-connect" style={{ fontSize: "18px", color: "#FF9600" }}></i>
                            <Text ml="2">MON-SAT 08AM  05PM</Text>
                        </Flex>
                    </Flex>
                    <Flex flex="1" direction="column" mt={["8", "0"]}>
                        <Text fontSize="20px" fontWeight="bold" color="#FF9600">$77.45B</Text>
                        <Text color="#636363" fontSize="12px" mt="3">MARKET CAP</Text>
                        <Text fontSize="20px" fontWeight="bold" color="#FF9600" mt="9">1726</Text>
                        <Text color="#636363" fontSize="12px" mt="3">ACTIVE ACCOUNTS</Text>
                    </Flex>
                    <Flex flex="1" direction="column" mt={["5", "0"]}>
                        <Text fontSize="20px" fontWeight="bold" color="#FF9600">165K</Text>
                        <Text color="#636363" fontSize="12px" mt="3">DAILY TRANSACTIONS</Text>
                        <Text fontSize="20px" fontWeight="bold" color="#FF9600" mt="9">127</Text>
                        <Text color="#636363" fontSize="12px" mt="3">SUPPORTED COUNTRIES</Text>
                    </Flex>
                </Flex>
                <Flex w="100%" justify="space-between" borderTop={["1px solid #C3C3C3", "none"]} align="center" mt={["8", "12"]} mb="4" pt={["8", "0"]} direction={["column", "row"]}>
                    <Text color="#707070" mb={["5", "0"]} fontSize="14px" textAlign={["center", "left"]}> 2022 WORLDINVEST TRADE. All rights reserved.</Text>
                    <Flex align="center" color="#707070"fontSize="24px">
                        <i className="mdi mdi-facebook" style={{ marginRight: "20px", cursor: "pointer" }} onClick={() => window.open("", "_BLANK")}></i>
                        <i className="mdi mdi-twitter" style={{ marginRight: "20px", cursor: "pointer" }} onClick={() => window.open("", "_BLANK")}></i>
                        <i className="mdi mdi-instagram" style={{ marginRight: "20px", cursor: "pointer" }} onClick={() => window.open("", "_BLANK")}></i>
                        <i className="mdi mdi-linkedin" style={{ marginRight: "20px", cursor: "pointer" }} onClick={() => window.open("", "_BLANK")}></i>
                        <i className="mdi mdi-google-plus" style={{ cursor: "pointer" }} onClick={() => window.open("", "_BLANK")}></i>
                    </Flex>
                </Flex>
            </Flex>
        </>
    )
}



export default Footer;