import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner, Box } from "@chakra-ui/react";
import { ApiFile } from "./request";
import Toast from "./toast";


export default function Transfer({ setPageTitle, setSelected, tData, setTData, userData, loadUser }) {
    const [loading, setLoading] = useState(false)
    useEffect(() => {
        setSelected(3)
        setPageTitle("Send Fund")
    }, [])
    console.log(tData)

    async function sendFund(e) {
        e.preventDefault()
        if(document.getElementById('ramount').value == 0) {
            Toast("Cannot transfer 0 amount", "error")
        }
        else if(parseInt(document.getElementById('ramount').value) > parseInt(userData[0]?.bal)) {
            Toast("Insufficient Balance", "error")
        }
        else if(userData[0]?.uname == document.getElementById('rname').value) {
            Toast("Cannot transfer to your own account", "error")
        }
        else {
            const formData = new FormData()
            formData.append("rname", document.getElementById('rname').value)
            formData.append("ramount", document.getElementById('ramount').value)
            formData.append("uid", userData[0]?.uid)
            setLoading(true)
            const res = await ApiFile("post", "others.php", formData)
            if(res?.status) {
                await loadUser()
                setTData([res?.data])
                Toast(res?.message, "success")
            }
            else {
                Toast(res?.message, "error")
            }
            setLoading(false)
        }
    }

    return (
        <Flex w="100%" justify="space-between" align="flex-start" direction={["column", "row"]}>
            <Flex w={["100%", "32%"]} boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3" bg="#fff">
                <Flex py="5" px="6" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Fund Transfer</Flex>
                <Flex w="100%" py="4" px="6" direction="column">
                    <form style={{ width: "100%" }} onSubmit={(e) => sendFund(e)}>
                        <Flex w="100%">
                            <Flex bg="#e8e8e8" borderTopLeftRadius=".25rem" borderBottomLeftRadius=".25rem" padding=".375rem .75rem" color="#6c757d" fontWeight="bold" align="center" justify="center"><i className="mdi mdi-account" style={{ color: "#000", fontSize: "20px" }}></i></Flex>
                            <input style={{ background: "#fff", color: "#6c757d", borderTopRightRadius: "0.25rem", borderBottomRightRadius: "0.25rem", border: "1px solid #e8e8e8", padding: ".6rem 1rem", width: "100%" }} className="wInput" type="text" placeholder="Username" required id="rname" />
                        </Flex>

                        <Flex w="100%" mt="3">
                            <Flex bg="#e8e8e8" borderTopLeftRadius=".25rem" borderBottomLeftRadius=".25rem" padding=".375rem .75rem" color="#6c757d" fontWeight="bold" align="center" justify="center"><i className="mdi mdi-currency-usd" style={{ color: "#000", fontSize: "20px" }}></i></Flex>
                            <input style={{ background: "#fff", color: "#6c757d", borderTopRightRadius: "0.25rem", borderBottomRightRadius: "0.25rem", border: "1px solid #e8e8e8", padding: ".6rem 1rem", width: "100%" }} className="wInput" type="number" placeholder="Enter amount you want to send" required id="ramount" />
                        </Flex>

                        <Flex w="100%" mt="5">
                            <button type="submit" style={{ background: "transparent", border: "none" }}><Flex bg="#1269DB" cursor="pointer" padding="6px 15px" fontSize="14px" borderRadius="3px" color="#fff" transition="300ms ease-in-out" _hover={{ opacity: "0.8" }} mr="1">{loading ? <Spinner color="#fff" size="sm" emptyColor="lightgrey" /> : "Send"}</Flex></button>
                        </Flex>
                    </form>
                </Flex>
            </Flex>
            <Flex mt={["7", "0"]} w={["100%", "65%" ]}boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3" bg="#fff">
                <Flex py="5" px="6" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Transfer History</Flex>
                
                <Box w={["310px", "100%"]} overflow={["auto", "hidden"]}>
                    <Flex py="5" px="6" direction="column" minWidth={["600px", "100%"]}>
                        <Flex w="100%">
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="25%">Sender</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="25%">Reciever</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="25%">Amount</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="25%">Date</Text>
                        </Flex>
                        {
                            tData[0]?.map((item, index) => (
                                <Flex w="100%" key={index}mb="2">
                                    <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="25%">{userData[0]?.uname}</Text>
                                    <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="25%">{item?.reciever}</Text>
                                    <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="25%">${parseInt(item?.amount)?.toLocaleString()}</Text>
                                    <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="25%">{item?.transdate}</Text>
                                </Flex>
                            ))
                        }
                    </Flex>
                </Box>
            </Flex>

        </Flex>
    )
}