import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner } from "@chakra-ui/react";
import Logo from "./logo.png";
import { useHistory } from "react-router-dom";
import { ApiRequest } from "./request";
import Toast from "./toast";


function Login() {
	const [loading, setLoading] = useState(false)
	const [err, setErr] = useState(false)
	useEffect(() => {
		document.title = "worldinvestbitcoin - Best Cryptocurrency Investment Platform"
		setTimeout(function(){
			sessionStorage.removeItem("registerModal")
		}, 3000)
	}, [])
	async function loginFunc(e) {
		e.preventDefault()
		var email = document.getElementById('logemail').value
		var pass = document.getElementById('logpass').value
		setLoading(true)
		const res = await ApiRequest("post", "login.php", {
			email: email,
			pass: pass,
		})
		if(res?.status === true) {
			sessionStorage.setItem("myid", res?.data?.uid)
			sessionStorage.setItem("worldIsLogged", true)
			history.push("/dashboard")
			Toast(res.message, "success")
		}
		else {
			setErr(res.message)
		}
		setLoading(false)
	}
	const history = useHistory()
	return (
		<Flex w="100%" bg="#fff" color="#495057" fontFamily="'Lato', sans-serif">
			<Flex minHeight="100vh" w="50%" className="bg4" display={["none", "flex"]}>
				<Flex bg="rgba(20, 20, 100, .8)" w="100%" minHeight="100%" justify="center" align="center" direction="column" color="#fff">
					<Image src={Logo} h="70px" mt="-10%" />
					<Text fontSize="1.725rem" fontWeight="bold">worldinvestbitcoin</Text>
					<Text fontSize="1.0375rem" mt="5">Best Cryptocurrency Investment Platform</Text>
				</Flex>
			</Flex>
			<Flex minHeight="100vh" w={["100%", "50%"]} bg="#fff" direction="column" px="8" pb="6" pt={["12", "6"]}>
				<Flex w="100%" direction="column" align="center">
					<Image src={Logo} h="70px" />
					<Flex align="center" color="#575962">
						<i className="mdi mdi-key-variant" style={{ fontSize: "25px" }}></i>
						<Text fontSize="1.1625rem" ml="1">User Login</Text>
					</Flex>
				</Flex>
				<Flex w="100%" bg="rgba(0,0,0,.1)" mt="3" mb="8" h="1px"></Flex>
				{
					sessionStorage.getItem("registerModal") === "true" && err === false &&
					<Flex mt="-2" mb="5" w="100%" bg="#fff" borderLeft="4px solid #31ce36" padding=".95rem 1.25rem" boxShadow="1px 1px 14px 0 rgb(18 38 63 / 26%)" fontSize="14px">Registration Successful. Please check your email address inbox or spam folder to confirm and activate your account.</Flex>
				}
				{
					err !== false &&
					<Flex mt="-2" mb="5" w="100%" bg="#fff" borderLeft="4px solid #f25961" padding=".95rem 1.25rem" boxShadow="1px 1px 14px 0 rgb(18 38 63 / 26%)" fontSize="14px">{err}</Flex>
				}
				<form onSubmit={(e) => loginFunc(e)}>
					<Text color="#495057" mb="2" fontSize="1rem">E-Mail Address</Text>
					<input type="email" placeholder="E-Mail Address" style={{ width: "100%", border: "1px solid #CCF", padding: "10px", color: "#495057" }} required id="logemail" />

					
					<Text color="#495057" mb="2" mt="5" fontSize="1rem">Password</Text>
					<input type="password" placeholder="Password" style={{ width: "100%", border: "1px solid #CCF", padding: "10px", color: "#495057" }} required id="logpass" />

					<Flex mt="3"><input type="checkbox" /><Text ml="2">Remember Me</Text></Flex>

					<Flex w="100%" justify="center" mt="1" direction="column" align="center">
						<button type="submit" style={{ background: "transparent", border: "none" }}><Flex cursor="pointer" bg="#1572e8" color="#fff" fontSize="14px" borderRadius="3px" padding=".65rem 1.4rem" _hover={{ opacity: "0.9" }} transition="300ms ease-in-out">{loading ? <Spinner color="#fff" emptyColor="lightgrey" /> : "Login"}</Flex></button>
						<Flex cursor="pointer" color="#007bff" fontSize="14px" mt="3" transition="300ms ease-in-out" _hover={{ textDecoration: "underline" }} onClick={() => history.push("/password-reset")}>Forgot Your Password?</Flex>
						<Flex mt="3">Don't have an account?<Text ml="2" cursor="pointer" color="#1572e8" fontWeight="bold" onClick={() => history.push("/register")}>Register</Text></Flex>
					</Flex>
				</form>
			</Flex>
		</Flex>
	)
}


export default Login;