import { useState, useEffect } from "react";
import {
	BrowserRouter as Router,
	Switch,
	Route,k
  } from "react-router-dom";
import { ChakraProvider, Flex, Text, Image, Spinner } from "@chakra-ui/react";
import "./App.scss";
import "./mdi/css/materialdesignicons.css";
import Nav from "./nav.js";
import Home from "./home.js";
import About from "./about.js";
import Signup from "./signup.js";
import Login from "./login.js";
import Contact from "./contact.js";
import ResetPassword from "./reset";
import Logo from "./logo.png";
import Aos from "aos";
import "aos/dist/aos.css";
import ScrollToTop from "./sc";
import LoadPage from "./loaderpage";
import Dashboard from "./dashboard";
import ConfirmMail from "./confirm";

function App() {
	const [isLoading, setIsLoading] = useState(false);
    const [selected, setSelected] = useState(0)
    const isLogged = sessionStorage.getItem("worldIsLogged")
	useEffect(() => {
		if(isLogged === null) {
			setIsLoading(true)
			setTimeout(function() {
				setIsLoading(false)
			}, 2000)
		}
	}, []);
	
	useEffect(() => {
		Aos.init({ duration: 1000 });
	  }, []);

	return (
		<ChakraProvider>
			<Flex w="100%" direction="column" fontFamily='"DM Sans", Sans-serif' color="#fff">
				{
					isLoading ?
					<Flex w="100%" h="100vh" bg="#000000" justify="center" align="center">
						<Image className="incs" src={Logo} w="150px" h="150px" mb={["12", "0"]} />
					</Flex>
					:
					<Router>					
						<ScrollToTop>
							<Flex w="100%" direction="column">
								<Switch>
									<Route exact path="/"><LoadPage /></Route>
									<Route path="/index">
										<Nav selected={selected} setSelected={setSelected} />
										<Home selected={selected} setSelected={setSelected} />
									</Route>
									<Route path="/dashboard"><Dashboard /></Route>
									<Route path="/about-us">
										<Nav selected={selected} setSelected={setSelected} />
										<About selected={selected} setSelected={setSelected} />
									</Route>
									<Route path="/register"><Signup /></Route>
									<Route path="/login"><Login /></Route>
									<Route path="/contact-us">
										<Nav selected={selected} setSelected={setSelected} />
										<Contact selected={selected} setSelected={setSelected} />
									</Route>
									<Route path="/password-reset"><ResetPassword /></Route>
									<Route path="/registration-confirm/:actid"><ConfirmMail /></Route>
									<Route><LoadPage /></Route>
								</Switch>
							</Flex>
						</ScrollToTop>
					</Router>
				}
			</Flex>
		</ChakraProvider>
	)
}


export default App;