import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner } from "@chakra-ui/react";
import opt from "./opt.png";
import inv1 from "./inv1.png";
import inv2 from "./inv2.png";
import inv3 from "./inv3.png";
import Toast from "./toast";


export default function Overview({ userData, setPageTitle, setSelected }) {
    useEffect(() => {
        setSelected(0)
        setPageTitle("Dashboard")
    }, [])
    return (
        <Flex w="100%" direction="column">
            <Flex w="100%" justify="space-between" align="flex-start" direction={["column", "row"]}>
                <Flex w={["100%", "62%"]} boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3" bg="#fff">
                    <Flex py="5" px="6" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6" direction="column">User Activities<Text color="#8d9498" mt="2" fontSize="14px">actions performed by you</Text></Flex>
                    <Flex py="5" px="6" direction="column">
                        <Flex w="100%">
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="50%" mr="2">Action</Text>
                            <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="50%">Status</Text>
                        </Flex>
                        {
                            userData[0]?.wd > 0 ?
                            <Flex w="100%">
                                <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="50%" mr="2">You made a withdrawal request</Text>
                                <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="50%">Request is being processed</Text>
                            </Flex>
                            :
                            <Flex w="100%">
                                <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="50%" mr="2">None</Text>
                                <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="50%">No action yet!</Text>
                            </Flex>
                        }
                    </Flex>
                </Flex>

                <Flex width={["100%", "35%"]} mt={["7", "0"]} direction="column">
                    <Flex w="100%" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" px="6" py="5" bg="#1572e8" color="#fff">
                        <Text fontWeight="bold" fontSize="20px">Withdrawal Stats</Text>
                        <Text fontWeight="bold" fontSize="20px" mt="4">${parseInt(userData[0]?.bal).toLocaleString()}</Text>
                        <Text mb="12" fontSize="14px" mt="4">Total withdraw actions: {parseInt(userData[0]?.wd).toLocaleString()}</Text>
                    </Flex>

                    <Flex w="100%" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" px="6" py="5" bg="#fff" align="center" mt="7">
                        <Flex w="35%" direction="column" align="center">
                            <Flex direction="column" align="center">
                                <svg xmlns="http://www.w3.org/2000/svg" width="90" height="90"><path fill="transparent" stroke="#f1f1f1" stroke-width="7" d="M 44.99154756204665 3.500000860767564 A 41.5 41.5 0 1 1 44.942357332570026 3.500040032273624 Z" class="circles-maxValueStroke"></path><path fill="transparent" stroke="#F25961" stroke-width="7" d="M 44.99154756204665 3.500000860767564 A 41.5 41.5 0 0 1 47.5629156305626 3.579214596164192 " class="circles-valueStroke"></path></svg>
                                <Flex fontSize="31.5px" align="center" justify="center" color="#575962" h="90px" position="absolute">{parseInt(userData[0]?.wd).toLocaleString()}</Flex>
                            </Flex>
                            <Text fontSize=".825rem" fontWeight="bold" mt="1rem">My Actions</Text>
                        </Flex>
                    </Flex>
                </Flex>
            </Flex>

            <Flex w="100%" mt="7" display={["none", "flex"]}>
                <Image w="100%" src={opt} onClick={() => {
                    Toast("Conatact Admin to proceed with investment", "success")   
                    if(window.tidioChatApi) {
                        setTimeout(function(){
                            window.tidioChatApi.open()
                        }, 1000)
                    }
                }} />
            </Flex>

            <Flex w="100%" mt="7" display={["flex", "none"]} direction="column">
                <Image w="100%" mb="4" src={inv1} onClick={() => {
                    Toast("Conatact Admin to proceed with investment", "success")   
                    if(window.tidioChatApi) {
                        setTimeout(function(){
                            window.tidioChatApi.open()
                        }, 1000)
                    }
                }} />
                <Image mb="4" w="100%" src={inv2} onClick={() => {
                    Toast("Conatact Admin to proceed with investment", "success")   
                    if(window.tidioChatApi) {
                        setTimeout(function(){
                            window.tidioChatApi.open()
                        }, 1000)
                    }
                }} />
                <Image w="100%" src={inv3} onClick={() => {
                    Toast("Conatact Admin to proceed with investment", "success")   
                    if(window.tidioChatApi) {
                        setTimeout(function(){
                            window.tidioChatApi.open()
                        }, 1000)
                    }
                }} />
            </Flex>
        </Flex>
    )
}