import { useState, useEffect } from "react";
import {
	BrowserRouter as Router,
	Switch,
	Route,k
  } from "react-router-dom";
import { Flex, Image, Text, Spinner, Box } from "@chakra-ui/react";
import { useHistory } from "react-router-dom";
import Overview from "./overview";
import Profile from "./profile";
import Wallet from "./wallet";
import Transfer from "./transfer";
import Withdraw from "./withdraw";
import Downlines from "./downlines";
import Support from "./support";
import NavbarDashboard from "./navbar";
import Sidebar from "./sidebar";
import Notifications from "./notifications";
import { ApiRequest, ApiFile } from "./request";
import Stats from "./stats";
import DepBtn from "./depBtn";
import ScrollToTop from "./sc";


function Dashboard() {
	const [userData, setUserData] = useState("")
    const [pageTitle, setPageTitle] = useState("Dashboard")
    const [minBar, setMinBar] = useState(false)
    const [selected, setSelected] = useState(0)
    const [isModal, setIsModal] = useState(false)
    const [wData, setWData] = useState("")
    const [tData, setTData] = useState("")
    const [dData, setDData] = useState("")
    const isLogged = sessionStorage.getItem("worldIsLogged")
    const [isSidebar, setIsSidebar] = useState(false)
    const history = useHistory()
    useEffect(() => {
		document.title = "worldinvestbitcoin - Best Cryptocurrency Investment Platform"
        if(isLogged === null) {
            history.push("/index")
        }
        loadUser()
        loadWithdraw()
        loadTransfer()
        loadDeposit()
    }, [])
    
	async function loadUser() {
        const res = await ApiRequest("post", "loaduser.php", {
            uid: sessionStorage.getItem("myid"),
        })
        if(res?.status === true) {
			setUserData([res?.data])
        }
	}

    async function loadWithdraw() {
        const formData = new FormData()
        formData.append("uid", sessionStorage.getItem("myid"))
        formData.append("withdraw", "withdraw")
        const res = await ApiFile("post", "others.php", formData)
        if(res?.status === true) {
			setWData([res?.data])
        }
	}


    async function loadTransfer() {
        const formData = new FormData()
        formData.append("uid", sessionStorage.getItem("myid"))
        formData.append("transfer", "transfer")
        const res = await ApiFile("post", "others.php", formData)
        if(res?.status === true) {
			setTData([res?.data])
        }
	}

    async function loadDeposit() {
        const formData = new FormData()
        formData.append("uid", sessionStorage.getItem("myid"))
        formData.append("deposit", "deposit")
        const res = await ApiFile("post", "others.php", formData)
        if(res?.status === true) {
			setDData([res?.data])
        }
	}
    
    return (
        <Flex w="100%" minHeight="100vh" bg="#fff" color="#000">
            <Router>				
				<ScrollToTop>
                <NavbarDashboard minBar={minBar} setMinBar={setMinBar} userData={userData} setIsModal={setIsModal} isSidebar={isSidebar} setIsSidebar={setIsSidebar} />
                <Flex minHeight="100vh" w="100%">
                    <Sidebar minBar={minBar} selected={selected} userData={userData} isSidebar={isSidebar} setIsSidebar={setIsSidebar} />
                    <Flex minHeight="100%" direction="column" flex="1" bg="#f9fbfd">
                        <Flex pt="120px" pb="60px" bg="#000" px="30px" color="#fff" justify="space-between" align="center">
                            <Text fontWeight="bold" fontSize="1.35rem">{pageTitle}</Text>
                            <DepBtn />
                        </Flex>
                        {
                            selected !== 6 &&
                            <Flex px={["5", "30px"]} mt="-8" w="100%"><Stats userData={userData} isModal={isModal} setIsModal={setIsModal} setWData={setWData} loadUser={loadUser} /></Flex>
                        }
                        <Flex w="100%" px={["5", "30px"]} mt="7">
                            <Switch>
                                <Route exact path="/dashboard"><Overview setPageTitle={setPageTitle} setSelected={setSelected} userData={userData} /></Route>
                                <Route path="/dashboard/profile"><Profile setPageTitle={setPageTitle} setSelected={setSelected} userData={userData} setUserData={setUserData} /></Route>
                                <Route path="/dashboard/wallet"><Wallet setPageTitle={setPageTitle} setSelected={setSelected} dData={dData} /></Route>
                                <Route path="/dashboard/send_money"><Transfer setPageTitle={setPageTitle} setSelected={setSelected} tData={tData} setTData={setTData} userData={userData} loadUser={loadUser} /></Route>
                                <Route path="/dashboard/withdrawal"><Withdraw setPageTitle={setPageTitle} setSelected={setSelected} wData={wData} /></Route>
                                <Route path="/dashboard/downlines"><Downlines setPageTitle={setPageTitle} setSelected={setSelected} userData={userData} /></Route>
                                <Route path="/dashboard/ticket"><Support setPageTitle={setPageTitle} setSelected={setSelected} /></Route>
                                <Route path="/dashboard/notifications"><Notifications setPageTitle={setPageTitle} setSelected={setSelected} userData={userData} /></Route>
                                <Route ><Overview userData={userData} setPageTitle={setPageTitle} setSelected={setSelected} /></Route>
                            </Switch>
                        </Flex>
                        <Flex w="100%" mt="auto">
                            <Flex mt="12" w="100%" bg="#eaecea" padding="20px" justify="center">
                                <Text textAlign="center" fontSize="14px" color="#575962">Copyrightt &copy; <Text as="span" color="#007bff" cursor="pointer" onClick={() => history.push("/index")}>worldinvestbitcoin</Text> 2022. All Rights Reserved</Text>
                            </Flex>
                        </Flex>
                    </Flex>
                </Flex>
                </ScrollToTop>
            </Router>
        </Flex>
    )
}


export default Dashboard;