import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner } from "@chakra-ui/react";
import Logo from "./logo.png";
import { useHistory, useParams } from "react-router-dom";
import { ApiRequest } from "./request";


function ConfirmMail() {
    const [err, setErr] = useState(false)
    const { actid } = useParams()
	useEffect(() => {
		document.title = "Verify Account - Best Cryptocurrency Investment Platform"
        confirmFunc()
	}, [])
	async function confirmFunc() {
        const res = await ApiRequest("post", "confirm.php", {
            activate: actid,
        })
        if(res?.status === true) {
            setErr(true)
        }
        else {
            setErr("false")
        }
	}
	const history = useHistory()
    return(
		<Flex w="100%" bg="#fff" color="#495057" minHeight="100vh" fontFamily="'Lato', sans-serif">
            <Flex bg="rgba(20, 20, 100, .8)" w="100%" minHeight="100vh" justify="center" align="center" direction="column">
                <Flex w={["90%", "34%"]} bg="#fff" borderRadius="5px" px="8" py="6" direction="column">
                    <Flex w="100%" direction="column" align="center">
                        <Image src={Logo} h="70px" />
                        <Flex align="center" color="#575962">
                            <i className="mdi mdi-key-variant" style={{ fontSize: "25px" }}></i>
                            <Text fontSize="1.1625rem" ml="1">User Verification</Text>
                        </Flex>
                    </Flex>
                    <Flex mt="8" w="100%" bg="#fff" borderLeft={err === true ? "4px solid #31ce36" : err === false ? "4px solid #FF9600": "4px solid #f25961"} padding=".95rem 1.25rem" boxShadow="1px 1px 14px 0 rgb(18 38 63 / 26%)" fontSize="14px">{err === true ? "Account activation successful" : err === "false" ? "Invalid activation process" : <><Text>Verifying</Text> <Spinner color="#000" emptyColor="grey" ml="2" /></>}</Flex>

                    <Flex w="100%" mt="5" direction="column">
                        <Flex><button style={{ background: "transparent", border: "none" }}><Flex cursor="pointer" bg="#FF9600" color="#fff" fontSize="14px" borderRadius="3px" padding=".65rem 1.4rem" _hover={{ opacity: "0.9" }} transition="300ms ease-in-out" onClick={() => history.push("/login")}>Back to Login</Flex></button></Flex>
                    </Flex>


                </Flex>
            </Flex>
        </Flex>
    )
}

export default ConfirmMail;