import { useHistory } from "react-router-dom";


function LoadPage() {
    const history = useHistory()
    const isLogged = sessionStorage.getItem("worldIsLogged")
    sessionStorage.removeItem("registerModal")
    if(isLogged === null) {
        history.push("/index")
    }
    else {
        history.push("/dashboard")
    }
    return null
}


export default LoadPage;