import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { Flex, Text, Image, Spinner } from "@chakra-ui/react";
import Toast from "./toast";
import { ApiFile } from "./request";


export default function Stats({ userData, isModal, setIsModal, setWData, loadUser }) {
    const history = useHistory()
    const [md, setMd] = useState(true)
    const [loading, setLoading] = useState(false)

    async function withdrawFunc(e) {
        e.preventDefault()
        var a = document.getElementById('wAmount').value
        if(md && parseInt(a) > parseInt(userData[0]?.bal)) {
            Toast("Insufficient Wallet Balance", "error")
        }
        else if(md && parseInt(userData[0]?.bal) == parseInt(0)) {
            Toast("Cannot withdraw, 0 Wallet balance", "error")
        }
        else if(!md && parseInt(a) > parseInt(userData[0]?.balRef)) {
            Toast("Insufficient Referral Balance", "error")
        }
        else if(!md && parseInt(userData[0]?.balRef) == parseInt(0)) {
            Toast("Cannot withdraw, 0 Referral balance", "error")
        }
        else {
            const formData = new FormData()
            formData.append("amount", document.getElementById('wAmount').value)
            formData.append("wallet", document.getElementById('wAcc').value)
            formData.append("wtype", md ? "wallet" : "referral")
            formData.append("uid", userData[0]?.uid)
            setLoading(true)
            const res = await ApiFile("post", "others.php", formData)
            if(res?.status) {
                await loadUser()
                setWData([res?.data])
                setIsModal(false)
                Toast(res?.message, "success")
            }
            else {
                Toast(res?.message, "error")
            }
            setLoading(false)
        }
    }

    return (
        <>
            <Flex w="100%" justify="space-between" direction={["column", "row"]}>
                <Flex w={["100%", "48%"]} px="25px" bg="#fff" borderRadius="5px" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" pt="6" pb="8" wordWrap="break-word" direction="column">
                    <Text color="#575962" fontSize="20px">Overall statistics</Text>
                    <Flex w="100%" mt="1.5rem" color="#575962" justify="space-between">
                        <Flex w={["5%", "20%"]}></Flex>
                        <Flex w="35%" direction="column" align="center">
                            <Flex direction="column" align="center">
                                <svg xmlns="http://www.w3.org/2000/svg" width="90" height="90"><path fill="transparent" stroke="#f1f1f1" stroke-width="7" d="M 44.99154756204665 3.500000860767564 A 41.5 41.5 0 1 1 44.942357332570026 3.500040032273624 Z" class="circles-maxValueStroke"></path><path fill="transparent" stroke="#2BB930" stroke-width="7" d="M 44.99154756204665 3.500000860767564 A 41.5 41.5 0 0 1 47.5629156305626 3.579214596164192 " class="circles-valueStroke"></path></svg>
                                <Flex fontSize="31.5px" align="center" justify="center" color="#575962" h="90px" position="absolute">{parseInt(userData[0]?.wd).toLocaleString()}</Flex>
                            </Flex>
                            <Text fontSize=".825rem" fontWeight="bold" mt="1rem">Withdrawals</Text>
                        </Flex>
                        <Flex w="35%" direction="column" align="center" mr="8">
                            <Flex direction="column" align="center">
                                <svg xmlns="http://www.w3.org/2000/svg" width="90" height="90"><path fill="transparent" stroke="#f1f1f1" stroke-width="7" d="M 44.99154756204665 3.500000860767564 A 41.5 41.5 0 1 1 44.942357332570026 3.500040032273624 Z" class="circles-maxValueStroke"></path><path fill="transparent" stroke="#F25961" stroke-width="7" d="M 44.99154756204665 3.500000860767564 A 41.5 41.5 0 0 1 47.5629156305626 3.579214596164192 " class="circles-valueStroke"></path></svg>
                                <Flex fontSize="31.5px" align="center" justify="center" color="#575962" h="90px" position="absolute">0</Flex>
                            </Flex>
                            <Text fontSize=".825rem" fontWeight="bold" mt="1rem">Downlines</Text>
                        </Flex>
                    </Flex>
                </Flex>


                <Flex w={["100%", "48%"]} mt={["6", "0"]} px="25px" bg="#fff" borderRadius="5px" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" pt="6" pb="8" wordWrap="break-word" direction="column">
                    <Text color="#575962" fontSize="20px">Balances</Text>
                    <Flex w="100%" mt="1.5rem" justify="space-between" align="center">
                        <Flex w={["100%", "45%"]} direction="column" pb="5" cursor="pointer" borderBottom="1px solid rgba(0,0,0,.125)" onClick={() => {
                            setMd(true)
                            setIsModal(true)
                        }}>
                            <Text color="#31ce36" fontSize="1.0375rem" fontWeight="bold">WALLET</Text>
                            <Text mt="2" fontSize="1.1625rem" fontWeight="bold" color="#1572e8" lineHeight="1.4">{userData[0]?.bal ? "$ "+parseInt(userData[0]?.bal)?.toLocaleString()+".00" : "$ 0.00"}</Text>
                            <Text fontSize="10px" color="#1572e8">Click to Withdraw Fund</Text>
                        </Flex>
                        <Flex borderBottom="1px solid rgba(0,0,0,.125)" w="50%" display={["none", "flex"]}></Flex>
                    </Flex>
                    <Flex w={["100%", "45%"]} mt="5" direction="column" pb="5" cursor="pointer" borderBottom={["1px solid rgba(0,0,0,.125)", "none"]} onClick={() => {
                            setMd(false)
                            setIsModal(true)
                        }}>
                        <Text color="#31ce36" fontSize="1.0375rem" fontWeight="bold">REFERRAL BONUS</Text>
                        <Text mt="2" fontSize="1.1625rem" fontWeight="bold" color="#1572e8" lineHeight="1.4">{userData[0]?.balRef ? "$ "+parseInt(userData[0]?.balRef)?.toLocaleString()+".00" : "$ 0.00"}</Text>
                        <Text fontSize="10px" color="#1572e8">Click to Withdraw Fund</Text>
                    </Flex>
                </Flex>
            </Flex>

            {
                isModal &&
                <Flex w="100%" h="100%" top="0" left="0" position="fixed" zIndex="100" bg="rgba(0,0,0,.6)" justify="center" align="center" direction="column">
                    <Flex w={["90%", "33.3%"]} bg="#fff" borderRadius="5px" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" direction="column" pt="8" px="3" color="#575962" maxHeight="80%" mt="6">
                        <Flex w="100%" direction="column" align="center" pb="5" borderBottom="1px solid rgba(0,0,0,0.04)">
                            <Text fontSize="1.1625rem" fontWeight="bold">{md ? "Wallet Withdrawal" : "Referral Withdrawal"}</Text>
                            <Text mt="2" color="#f25961" fontSize=".9125rem">{md ? "Available Balance:" : "Total Earning: $"+parseInt(userData[0]?.balRef)?.toLocaleString()+".00"}</Text>
                            {
                                md &&
                                <Text mt="1" fontSize="1.1625rem" fontWeight="bold">{"$ "+parseInt(userData[0]?.bal)?.toLocaleString()+".00"}</Text>
                            }
                        </Flex>
                        <Flex w="100%" direction="column" px={["5", "8"]} py="8">
                            <Text textAlign="center" fontSize=".9125rem">Enter amount and select bank/wallet below</Text>
                            <form onSubmit={(e) => withdrawFunc(e)}>
                                <Flex mt="6">
                                    <Flex bg="#1F2E86" borderTopLeftRadius=".25rem" borderBottomLeftRadius=".25rem" padding=".375rem .75rem" color="#fff" fontWeight="bold"><i className="mdi mdi-currency-usd" style={{ fontSize: "20px" }}></i></Flex>
                                    <input style={{ fontSize: "14px", padding: ".6rem 1rem", borderTopRightRadius: ".25rem", borderBottomRightRadius: ".25rem", color: "#495057", border: "1px solid rgba(0,0,0,0.1)", width: "100%", color: "#495057" }} className="wInput" type="number" placeholder="Enter Amount to withdraw" required id="wAmount" />
                                </Flex>

                                <Flex mt="3">
                                    <Flex bg="#1F2E86" borderTopLeftRadius=".25rem" borderBottomLeftRadius=".25rem" padding=".375rem .75rem" color="#fff" fontWeight="bold"><i className="mdi mdi-home" style={{ fontSize: "20px" }}></i></Flex>
                                    <select style={{ fontSize: "14px", padding: ".6rem 1rem", borderTopRightRadius: ".25rem", borderBottomRightRadius: ".25rem", color: "#495057", border: "1px solid rgba(0,0,0,0.1)", width: "100%", color: "#495057" }} className="wInput" type="number" placeholder="Enter Amount to withdraw" required id="wAcc">
                                        <option value=""></option>
                                        {
                                            userData[0]?.bankname !== "" &&
                                            <option value={userData[0]?.accname}>Bank -&gt; {userData[0]?.accname}</option>
                                        }
                                        {
                                            userData[0]?.coinname !== "" &&
                                            <option value={userData[0]?.wallet}>Wallet -&gt; {userData[0]?.wallet} wallet</option>
                                        }
                                    </select>
                                </Flex>

                                <Flex mt="12" justify="center" w="100%">
                                    <button type="submit" style={{ background: "transparent", border: "none" }}><Flex bg="#0A4FA8" cursor="pointer" padding="5px 15px" fontSize="14px" borderRadius="3px" color="#fff" transition="300ms ease-in-out" _hover={{ opacity: "0.8" }} mr="1">{loading ? <Spinner color="#fff" size="sm" emptyColor="lightgrey" /> : "Withdraw"}</Flex></button>

                                    <Flex bg="#f25961" cursor="pointer" padding="5px 15px" fontSize="14px" borderRadius="3px" color="#fff" transition="300ms ease-in-out" _hover={{ opacity: "0.8" }} onClick={() => setIsModal(false)}>Cancel</Flex>
                                </Flex>
                            </form>
                        </Flex>
                    </Flex>
                </Flex>
            }
        </>
    )
}