import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner } from "@chakra-ui/react";
import img11 from "./img11.png";
import img15 from "./img15.png";
import img16 from "./img16.png";
import img17 from "./img17.png";
import img18 from "./img18.png";
import mimg14 from "./mimg14.png";
import mimg15 from "./mimg15.png";
import Footer from "./footer";

function About({ selected, setSelected }) {
	useEffect(() => {
		document.title = "About Us  WORLDINVEST TRADE"
	}, [])
	setSelected(1)
	return (
		<Flex w="100%" direction="column">
            <Flex w="100%" h={["55vh", "80vh"]} className="bg3">
                <Flex w="100%" h="100%" justify="center" px="8%" direction="column" bg="rgba(0,0,0,0.6)" pt="8%">
                    <Text fontWeight="bold" data-aos="fade-up" fontFamily="'Poppins', sans-serif" fontSize={["45px", "80px"]} mb={["4", "10"]}>About Us</Text>
                    <Flex h="2px" w={["139px", "230px"]} bg="#707070"></Flex>
                </Flex>
            </Flex>
            <Flex bg="#000" px={["5", "12"]} direction="column" w="100%" py="3">
				<Flex justify="space-between" direction={["column", "row"]}>
					<Flex w={["100%", "48%"]}>
						<Image src={img11} w="100%" data-aos="fade-right" />
					</Flex>
					<Flex w={["100%", "49%"]} mt={["5", "0"]} direction="column">
						<Text fontWeight="bold" fontSize="25px" mb="4" mt="3">WE ARE WORLDINVEST</Text>
						<Text mb="10" color="#636363">A place for everyone who wants to simply trade Cryptocurrencies. Deposit funds using Cryptocurrency or bank transfer. Instant buying/selling of Cryptocurrencies at fair price is guaranteed. Nothing extra. Join over 700,000 users from all over the world satisfied with our services.</Text>
						<Text fontWeight="bold" fontSize="25px" mb="4">RISK WARNING</Text>
						<Text color="#636363">Bitcoin is not legal tender and is not backed by any government. Accounts and value balances are not subject to any government backed deposit insurance or any other government protections.</Text>
					</Flex>
				</Flex>
			</Flex>
			<Flex w="100%" px={["5", "15%"]} mt={["5", "0"]}><Image w="100%" src={img15} /></Flex>

			<Flex w="100%" mt="12" data-aos="fade-up"><Image w="100%" src={img17} display={["none", "block"]} /><Image w="100%" src={mimg15} display={["block", "none"]} /></Flex>

			<Flex w="100%"><Image w="100%" src={img18} display={["none", "block"]} /><Image w="100%" src={mimg14} display={["block", "none"]} /></Flex>

            <Flex w="100%" mt="5" justify="center" pt="8" pb="6" color="#9DB2A6" fontSize="13px">
				<Text cursor="pointer" color="#1572e8" onClick={() => window.open("https://www.tradingview.com/markets/", "_BLANK")} mr="2">Financial Markets</Text>
				by TradingView
			</Flex>
			<Flex w="100%" px={["5", "15%"]} data-aos="fade-up"><Image w="100%" src={img16} /></Flex>
			<Footer selected={selected} setSelected={setSelected} />
        </Flex>
	)
}


export default About;