import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner } from "@chakra-ui/react";
import Logo from "./logo.png";
import { useHistory } from "react-router-dom";

function Nav({ selected, setSelected }) {
    const history = useHistory()
    const [menuOpen, setMenuOpen] = useState(false)
    const routes = [
        {
            name: "Home",
            link: "/index",
        },
        {
            name: "About Us",
            link: "/about-us",
        },
        {
            name: "Sign Up",
            link: "/register",
        },
        {
            name: "Login",
            link: "/login",
        },
        {
            name: "Contact Us",
            link: "/contact-us",
        },
    ]
	return (
		<Flex w="100%" position="fixed" top="0" left="0" bg="#000000" py="3" pr="5" pl="3" align="center" zIndex="1000">
            <Flex flex="1" align="center">
                <Image src={Logo} w="48px" h="48px" cursor="pointer" onClick={() => {
                    setSelected(0)
                    history.push("/index")
                }} />
            </Flex>
            <Flex flex="1" display={["flex", "none"]} align="center" justify="flex-end">
                <Flex bg="#FE971A" borderRadius="3px" cursor="pointer" padding="0 .4em" onClick={() => menuOpen ? setMenuOpen(false) : setMenuOpen(true)}>
                    <i className={menuOpen ? "mdi mdi-close" : "mdi mdi-menu"} style={{ fontSize: "25px", color: "#000"}}></i>
                </Flex>
            </Flex>
            {
                menuOpen &&
                <Flex w="100%" direction="column" display={["flex", "none"]} position="absolute" left="0" bg="#FE971A" color="#000" fontSize="13px" mt="265px" data-aos="fade-down">
                    {
                        routes.map((item, index) => (
                            <Text cursor="pointer" key={index} color={index === selected ? "#FE971A" : "#000"} padding="10px 20px" w="100%" textAlign="center" transition="300ms ease-in-out" bg={index === selected ? "#000" : "#FE971A"} onClick={() => {
                                setSelected(index)
                                history.push(item.link)
                                setMenuOpen(false)
                            }}>{item.name}</Text>
                        ))
                    }
                </Flex>
            }
            <Flex flex="1" justify="flex-end" align="center" display={["none", "flex"]}>
                {
                    routes.map((item, index) => (
                        <Text cursor="pointer" key={index} color="#FE971A" padding="13px 20px" transition="300ms ease-in-out" _hover={{ borderBottom: "3px solid #FE971A" }} borderBottom={index === selected ? "3px solid #FE971A" : "3px solid transparent"} onClick={() => {
                            setSelected(index)
                            history.push(item.link)
                        }}>{item.name}</Text>
                    ))
                }
            </Flex>
        </Flex>
	)
}


export default Nav;