import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner, Box } from "@chakra-ui/react";


export default function Withdraw({ setPageTitle, setSelected, wData }) {
    useEffect(() => {
        setSelected(4)
        setPageTitle("Withdrawal")
    }, [])
    console.log(wData)

    return (
    <Flex w="100%" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3" bg="#fff">
        <Flex py="5" px="6" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Withdrawal History</Flex>
        
        <Box w={["310px", "100%"]} overflow={["auto", "hidden"]}>
            <Flex py="5" px="6" direction="column" minWidth={["600px", "100%"]}>
                <Flex w="100%">
                    <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="20%">Date</Text>
                    <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="20%">Package</Text>
                    <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="20%">Amount</Text>
                    <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="20%">Account</Text>
                    <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold" w="20%">Status</Text>
                </Flex>
                {
                    wData[0]?.map((item, index) => (
                        <Flex w="100%" key={index}mb="2">
                            <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="20%">{item?.transdate}</Text>
                            <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="20%">{item?.package}</Text>
                            <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="20%">${parseInt(item?.amount)?.toLocaleString()}</Text>
                            <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="20%">{item?.acc}</Text>
                            <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1" w="20%">{item?.status}</Text>
                        </Flex>
                    ))
                }
            </Flex>
        </Box>
    </Flex>
    )
}