import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner } from "@chakra-ui/react";
import Footer from "./footer";
import Collapsible from 'react-collapsible';
import emailjs from 'emailjs-com';

function Contact({ selected, setSelected }) {
    useEffect(() => {
		document.title = "Contact Us  WORLDINVEST TRADE"
	}, [])
	setSelected(4)
    const [loading, setLoading] = useState(false)
    const accord = [
        {
            title: "Who controls the Bitcoin network?",
            sub: "Nobody owns the Bitcoin network much like no one owns the technology behind email. Bitcoin is controlled by all Bitcoin users around the world.",
        },
        {
            title: "Is Bitcoin useful for illegal activities?",
            sub: "Cryptocurrencies have been used to launder the profits of online drug traffickers; they've been a tool to finance terrorism, says Treasury Secretary Janet Yellen. It's a tool of kidnappers and extortionists that is contrary to civilization, according to Berkshire Hathaway Vice President Charlie Munger.",
        },
        {
            title: "Doesn't Bitcoin unfairly benefit early adopters?",
            sub: "Doesn't Bitcoin unfairly benefit early adopters? ... There is no guarantee that the price of a bitcoin will increase or drop. This is very similar to investing in an early startup that can either gain value through its usefulness and popularity, or just never break through.",
        },
        {
            title: "What about Bitcoin and taxes?",
            sub: "Cryptocurrency is considered property for federal income tax purposes. And, for the typical investor, the IRS treats it as a capital asset. As a result, crypto taxes are no different than the taxes you pay on any other gain realized on the sale or exchange of a capital asset.",
        },
        {
            title: "What determines bitcoins price?",
            sub: "The price of a bitcoin is determined by supply and demand. When demand for bitcoins increases, the price increases, and when demand falls, the price falls.",
        },
    ]

    async function contactUs(e) {
		e.preventDefault()
        setLoading(true)
        var a = document.getElementById('fname').value
        var b = document.getElementById('email').value
        var c = document.getElementById('phone').value
        var d = document.getElementById('company').value
        var e = document.getElementById('message').value
        const templateParams = {
            from_name: "New User",
            message: "USER CONATCTED  \n WORLD INVEST BTC \n ____ Username is: "+a+" ______ Email is: "+b+" _____ PhoneNumber is: "+c+" ______ Company is: "+d+" _______ Message is: "+e
        }
        await emailjs.send('outlook', 'template_6ymwp0u', templateParams, 'vAHIWN6VU2ORgYGhh')
        .then((response) => {
            console.log('')
        }, (err) => {
            console.log('')
        })
        alert("Message delivered")
        setLoading(false)
    }

	return (
		<Flex w="100%" direction="column" color="#3d3d3d">
            <Flex w="100%" bg="#fff" px={["4", "12"]} pt={["90px", "8%"]} pb="4" direction="column">
                <Text color="#636363" fontWeight="bold" fontSize="3.3125em" fontFamily="'Poppins', sans-serif">Contact<Text color="#333" ml="3" as="span">Us</Text></Text>
                <Flex w="50px" h="1.5px" bg="#BBB" mt="4"></Flex>

                <Text mt="8" fontWeight="bold" fontSize="2.375em">HOW TO</Text>
                
                <Text mt="5" color="#636363" fontWeight="bold" fontSize="1.6875em">Contact<Text color="#333" ml="3" as="span">Us</Text></Text>
                <Flex w="50px" h="1px" bg="#BBB" mt="4"></Flex>

                <Text fontFamily="'Poppins', sans-serif" color="#3d3d3d" mt="10" display={["block", "none"]}>
                    <Text>+1 (740)-312-7338</Text>
                    <Text cursor="pointer" _hover={{ color: "#1bb0ce" }} transition="300ms ease-in-out" onClick={() => window.open("mailto: info@worldinvestbitcoin.com")}>info@worldinvestbitcoin.com</Text>
                </Text>
                
                <Text mt="8" fontWeight="bold" fontSize="2.375em" color="#000">Send <Text display={["block", "none"]}></Text>us a Message</Text>
                <Flex w="50px" h="1px" bg="#BBB" mt="4"></Flex>

                

                <form onSubmit={(e) => contactUs(e)}>
                    <Flex w="100%" align="center" mb="50px" mt="5">
                        <Flex w={["100%", "50%"]}>
                            <input type="text" placeholder="Full name*" style={{ color: "#3d3d3d", padding: "1.4375em 0", background: "transparent", borderBottom: "3px solid #3D3D3D", width: "100%" }} id="fname" className="cInput" required />
                        </Flex>
                        <Text ml="5" fontFamily="'Poppins', sans-serif" color="#3d3d3d" display={["none", "block"]}>
                            <Flex w="50px" h="1px" bg="#BBB" mb="6"></Flex>
                            <Text>+1 (740)-312-7338</Text>
                            <Text cursor="pointer" _hover={{ color: "#1bb0ce" }} transition="300ms ease-in-out" onClick={() => window.open("mailto: info@worldinvestbitcoin.com")}>info@worldinvestbitcoin.com</Text>
                        </Text>
                    </Flex>
                    <Flex w={["100%", "50%"]} mb="50px">
                        <input type="email" placeholder="Email*" style={{ color: "#3d3d3d", padding: "1.4375em 0", background: "transparent", borderBottom: "3px solid #3D3D3D", width: "100%" }} id="email" className="cInput" required />
                    </Flex>
                    <Flex w={["100%", "50%"]} mb="50px">
                        <input type="tel" placeholder="Phone Number" style={{ color: "#3d3d3d", padding: "1.4375em 0", background: "transparent", borderBottom: "3px solid #3D3D3D", width: "100%" }} id="phone" className="cInput" required />
                    </Flex>
                    <Flex w={["100%", "50%"]} mb="50px">
                        <input type="text" placeholder="Company" style={{ color: "#3d3d3d", padding: "1.4375em 0", background: "transparent", borderBottom: "3px solid #3D3D3D", width: "100%" }} id="company" className="cInput" required />
                    </Flex>
                    <Flex w={["100%", "50%"]} mb="20px">
                        <textarea type="text" placeholder="Message" style={{ color: "#3d3d3d", padding: "1.4375em 0", background: "transparent", borderBottom: "3px solid #3D3D3D", width: "100%", resize: "none", height: "220px" }} id="message" className="cInput" required></textarea>
                    </Flex>
                    <Flex><button type="submit" style={{ background: "transparent", border: "none" }}><Flex padding="0.9375em 3.125em" fontWeight="bold" borderRadius="25px" bg="#3D3D3D" color="#fff" cursor="pointer" fontSize="0.8125em" _hover={{ bg: "#54595F"}} transition="300ms ease-in-out">{loading ? <Spinner color="#fff" emptyColor="lightgrey" /> : "SEND!"}</Flex></button></Flex>
                </form>

                <Text mt="12" fontWeight="bold" fontSize="2.375em" color="#000">WHAT IS</Text>
                
                <Text mt="5" color="#636363" fontWeight="bold" fontSize="1.6875em">Your Question</Text>
                <Flex w="50px" h="1px" bg="#BBB" mt="4"></Flex>
                <Flex w="100%" mt="5" color="#3d3d3d" fontFamily="'Poppins', sans-serif" direction="column" border="1px solid #BBB">
                        {
                            accord.map((item, index) => (
                                <Flex direction="column" key={index} borderBottom="1px solid #BBB">
                                    <Collapsible key={index} trigger={
                                        <Flex py="3" px="5" align="center" justify="space-between">{item.title}<i className="mdi mdi-chevron-down" style={{ fontSize: "30px", color: "#BBB" }}></i></Flex>
                                    } triggerWhenOpen ={
                                        <Flex py="3" px="5" align="center" justify="space-between">{item.title}<i className="mdi mdi-chevron-up" style={{ fontSize: "30px", color: "#BBB" }}></i></Flex>
                                    } open={index === 0 && true} transitionTime={300}>
                                        <Text borderTop="1px solid #BBB" py="5" px="5">{item.sub}</Text>
                                    </Collapsible>
                                </Flex>
                            ))
                        }
                </Flex>
            </Flex>
			<Footer selected={selected} setSelected={setSelected} />
        </Flex>
	)
}


export default Contact;