import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner, Box } from "@chakra-ui/react";
import acc from "./acc.png";
import { ApiFile } from "./request";
import Toast from "./toast";
import { countryListAllIsoData } from "./countries";
import front from "./front.png";
import back from "./back.png";
import qr from "./qr.png";

export default function Profile({ setPageTitle, setSelected, userData, setUserData }) {
    const [tab, setTab] = useState(0)
    const [selectedFile, setSelectedFile] = useState(null)
    const [selectedFile2, setSelectedFile2] = useState(null)
    const [selectedFile3, setSelectedFile3] = useState(null)
    const [selectedFile4, setSelectedFile4] = useState(null)
    const [selectedFile5, setSelectedFile5] = useState(null)
    const [load1, setLoad1] = useState(false)
    const [load2, setLoad2] = useState(false)
    const [load3, setLoad3] = useState(false)
    const [load4, setLoad4] = useState(false)
    const [load5, setLoad5] = useState(false)
    const [load6, setLoad6] = useState(false)
    const [load7, setLoad7] = useState(false)
    const [load8, setLoad8] = useState(false)
    const [idShow, setIdShow] = useState("")
    const [showFa, setShowFa] = useState(false)
    const tabs = ["Profile", "Banks", "Wallets", "KYC", "Security"]

    useEffect(() => {
        setSelected(1)
        setPageTitle("My Profile")
    }, [])

    async function uploadPhoto(e) {
        e.preventDefault()
        if(selectedFile !== null) {
            setLoad1(true)
            const formData = new FormData()
            formData.append("avatar", selectedFile)
            formData.append("uid", userData[0]?.uid)
            const res = await ApiFile("post", "upload.php", formData)
            if(res?.status) {
                setUserData([res?.data])
                Toast(res?.message, "success")
            }
            else {
                Toast(res?.message, "error")
            }
            setLoad1(false)
        }
        else {
            Toast("Tap on your avatar to upload a new profile photo", "error")
        }
    } 
    const handleFileSelect = (event) => {
        setSelectedFile(event.target.files[0])
    }

    async function updateProfile(e) {
        e.preventDefault()
        const formData = new FormData()
        formData.append("country", document.getElementById('country').value)
        formData.append("address", document.getElementById('address').value)
        formData.append("phone", document.getElementById('phone').value)
        formData.append("uid", userData[0]?.uid)
        setLoad2(true)
        const res = await ApiFile("post", "upload.php", formData)
        if(res?.status) {
            setUserData([res?.data])
            Toast(res?.message, "success")
        }
        else {
            Toast(res?.message, "error")
        }
        setLoad2(false)
    }

    async function addBank(e) {
        e.preventDefault()
        const formData = new FormData()
        formData.append("bankname", document.getElementById('bankname').value)
        formData.append("accno", document.getElementById('accno').value)
        formData.append("accname", document.getElementById('accname').value)
        formData.append("uid", userData[0]?.uid)
        setLoad3(true)
        const res = await ApiFile("post", "upload.php", formData)
        if(res?.status) {
            setUserData([res?.data])
            Toast(res?.message, "success")
        }
        else {
            Toast(res?.message, "error")
        }
        setLoad3(false)
    }

    async function addWallet(e) {
        e.preventDefault()
        const formData = new FormData()
        formData.append("coinname", document.getElementById('coinname').value)
        formData.append("coinhost", document.getElementById('coinhost').value)
        formData.append("wallet", document.getElementById('wallet').value)
        formData.append("uid", userData[0]?.uid)
        setLoad4(true)
        const res = await ApiFile("post", "upload.php", formData)
        if(res?.status) {
            setUserData([res?.data])
            Toast(res?.message, "success")
        }
        else {
            Toast(res?.message, "error")
        }
        setLoad4(false)
    }

    async function isfaFunc(e, data) {
        e.preventDefault()
        const formData = new FormData()
        formData.append("isfa", data)
        formData.append("uid", userData[0]?.uid)
        if(data === "1") {
            setLoad7(true)
        }
        else {
            setLoad8(true)
        }
        const res = await ApiFile("post", "upload.php", formData)
        if(res?.status) {
            setUserData([res?.data])
            Toast(res?.message, "success")
        }
        else {
            Toast(res?.message, "error")
        }
        if(data === "1") {
            setLoad7(false)
        }
        else {
            setLoad8(false)
        }
        setShowFa(false)
    }


    async function changePass(e) {
        e.preventDefault()
        const formData = new FormData()
        var a = document.getElementById('opass').value
        var b = document.getElementById('npass').value
        var c = document.getElementById('cpass').value
        if(b == c) {
            formData.append("opass", a)
            formData.append("npass", b)
            formData.append("uid", userData[0]?.uid)
            setLoad6(true)
            const res = await ApiFile("post", "upload.php", formData)
            if(res?.status) {
                setUserData([res?.data])
                Toast(res?.message, "success")
            }
            else {
                Toast(res?.message, "error")
            }
            setLoad6(false)
        }
        else {
            Toast("New Password doesn't match confirmed password", "error")
        }
    }

    async function addKyc(e) {
        e.preventDefault()
        const formData = new FormData()
        formData.append("selfie", selectedFile2)
        formData.append("cardfront", selectedFile3 !== null ? selectedFile3 : selectedFile2)
        formData.append("cardback", selectedFile4 !== null ? selectedFile4 : selectedFile2)
        formData.append("proofaddress", selectedFile5)
        formData.append("uid", userData[0]?.uid)
        setLoad5(true)
        const res = await ApiFile("post", "upload.php", formData)
        if(res?.status) {
            setUserData([res?.data])
            Toast(res?.message, "success")
        }
        else {
            Toast(res?.message, "error")
        }
        setLoad5(false)
    }

    return (
        <Flex w="100%" direction="column" bg="#fff" borderRadius="5px" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" py="8" px="4">
            <Flex w="100%" flexWrap={["wrap", "nowrap"]}>
                {
                    tabs.map((item, index) => (
                        <Flex mb={["3", "0"]} key={index} color={index === tab ? "#495057" : "#1572e8"} padding=".5rem 1rem" cursor="pointer" transition="color 300ms ease-in-out"  borderTopRightRadius=".25rem" borderTopLeftRadius=".25rem" borderBottom={index === tab ? "1px solid transparent" : "1px solid #dee2e6"} borderRight={index === tab ? "1px solid #dee2e6" : "1px solid transparent"} borderLeft={index === tab ? "1px solid #dee2e6" : "1px solid transparent"} borderTop={index === tab ? "1px solid #dee2e6" : "1px solid transparent"} onClick={() => setTab(index)} fontSize={["14px", "16px"]}>{item}</Flex>
                    ))
                }
                <Flex flex="1" borderBottom="1px solid #dee2e6" mb={["3", "0"]}></Flex>
            </Flex>
            <Flex w="100%" py={["7", "12"]} px={["2", "12"]} justify="space-between" align="flex-start" direction={["column", "row"]}>
                {
                    tab === 0 ?
                    <>
                        <Flex w={["100%", "35%"]} boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="12">
                            <Flex px="5" py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Avatar</Flex>
                            <Flex direction="column" py="5" px="5" align="center" color="#575962" w="100%">
                                <Image w="100px" h="100px" src={selectedFile !== null ? URL.createObjectURL(selectedFile) : userData[0]?.dp !== "" ? userData[0]?.dp : acc } onClick={() => document.getElementById('dp').click()} borderRadius="100%" cursor="pointer" />
                                <Text fontWeight="bold" mt="5">{userData[0]?.fname +" "+ userData[0]?.lname}</Text>
                                <Flex h="1px" w="100%" my="5" bg="#dee2e6"></Flex>
                                <Text fontWeight="bold">Referral link:</Text>
                                <Text color="#c60" mt="2" mb="5">/register/{userData[0]?.uname}</Text>
                                <form onSubmit={(e) => uploadPhoto(e)}>
                                    <input type="file" style={{ visibility: "hidden", width: "0", height: "0" }} id="dp" accept="image/x-png,image/jpeg" onChange={handleFileSelect} />
                                    <button type="submit" style={{ background: "transparent", border: "none" }}><Flex bg="#0A4FA8" cursor="pointer" padding="6px 15px" fontSize="14px" borderRadius="3px" color="#fff" transition="300ms ease-in-out" _hover={{ opacity: "0.8" }} mr="1">{load1 ? <Spinner color="#fff" size="sm" emptyColor="lightgrey" /> : "Update Photo"}</Flex></button>
                                </form>
                            </Flex>
                        </Flex>


                        <Flex w={["100%", "62%"]} mt={["7", "0"]} boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="12">
                            <Flex px="5" py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Profile Settings</Flex>
                            <Flex flexWrap="wrap" justify="space-between" px="5" py="5" direction={["column", "row"]}>
                                <Flex w={["100%", "46%"]} direction="column" mb="5">
                                    <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">First Name</Text>
                                    <input style={{ background: "#e8e8e8", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid transparent", padding: ".6rem 1rem", width: "100%" }} disabled value={userData[0]?.fname} />
                                </Flex>

                                
                                <Flex w={["100%", "46%"]} direction="column" mb="5">
                                    <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Last Name</Text>
                                    <input style={{ background: "#e8e8e8", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid transparent", padding: ".6rem 1rem", width: "100%" }} disabled value={userData[0]?.lname} />
                                </Flex>

                                
                                <Flex w={["100%", "46%"]} direction="column" mb="5">
                                    <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Email Address</Text>
                                    <input style={{ background: "#e8e8e8", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid transparent", padding: ".6rem 1rem", width: "100%" }} disabled value={userData[0]?.email} />
                                </Flex>

                                
                                <Flex w={["100%", "46%"]} direction="column" mb="5">
                                    <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Username</Text>
                                    <input style={{ background: "#e8e8e8", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid transparent", padding: ".6rem 1rem", width: "100%" }} disabled value={userData[0]?.uname} />
                                </Flex>

                                <form onSubmit={(e) => updateProfile(e)} style={{ width: "100%" }}>
                                    <Flex w="100%" flexWrap="wrap" justify="space-between" direction={["column", "row"]}>
                                        <Flex w={["100%", "46%"]} direction="column" mb="5">
                                            <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Country</Text>
                                            <select className="wInput" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #e8e8e8", padding: ".6rem 1rem", width: "100%" }} placeholder={userData[0]?.address} required id="country">
                                                <option value="">{userData[0]?.country}</option>
                                                {
                                                countryListAllIsoData.map((item, index) => (
                                                    <option key={index} value={item.country}>{item.country}</option>
                                                ))
                                                }
                                            </select>
                                        </Flex>

                                        <Flex w={["100%", "46%"]} direction="column" mb="5">
                                            <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Address</Text>
                                            <input className="wInput" type="text" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #e8e8e8", padding: ".6rem 1rem", width: "100%" }} placeholder={userData[0]?.address} required id="address" />
                                        </Flex>

                                        <Flex w={["100%", "46%"]} direction="column" mb="5">
                                            <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Phone</Text>
                                            <Flex w="100%"><Flex bg="#e8e8e8" borderTopLeftRadius=".25rem" borderBottomLeftRadius=".25rem" padding=".375rem .75rem" color="#6c757d" fontWeight="bold" align="center" justify="center">+</Flex>
                                            <input style={{ background: "#fff", color: "#6c757d", borderTopRightRadius: "0.25rem", borderBottomRightRadius: "0.25rem", border: "1px solid #e8e8e8", padding: ".6rem 1rem", width: "100%" }} className="wInput" type="tel" placeholder={userData[0]?.phone} required id="phone" /></Flex>
                                        </Flex>
                                    </Flex>
                                    <button type="submit" style={{ background: "transparent", border: "none" }}><Flex bg="#0A4FA8" cursor="pointer" padding="6px 15px" fontSize="14px" borderRadius="3px" color="#fff" transition="300ms ease-in-out" _hover={{ opacity: "0.8" }} mr="1">{load2 ? <Spinner color="#fff" size="sm" emptyColor="lightgrey" /> : "Update Profile"}</Flex></button>
                                </form>
                            </Flex>
                        </Flex>
                    </>
                    :
                    tab === 1 ? 
                    <Flex w="100%" direction="column">
                        <Flex w="100%" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3">
                            <Flex px="5" py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Add Bank  Details</Flex>
                            <Flex w="100%" py="5" px="5">
                                <form onSubmit={(e) => addBank(e)} style={{ width: "100%" }}>
                                    <Flex w="100%" justify="space-between" direction={["column", "row"]}>
                                        <Flex w={["100%", "47%"]} direction="column" mb="5">
                                            <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Bank Name</Text>
                                            <input className="wInput" type="text" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem" }} placeholder="Bank Name" required id="bankname" />
                                        </Flex>

                                        <Flex w={["100%", "47%"]} direction="column" mb="5">
                                            <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Account Number</Text>
                                            <input className="wInput" type="number" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem" }} placeholder="Account Number" required id="accno" />
                                        </Flex>
                                    </Flex>
                                    <Flex w="100%" direction="column" mb="5">
                                        <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Account Name</Text>
                                        <input className="wInput" type="text" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem" }} placeholder="Account Name" required id="accname" />
                                    </Flex>
                                    <button type="submit" style={{ background: "transparent", border: "none" }}><Flex bg="#0A4FA8" cursor="pointer" padding="6px 15px" fontSize="14px" borderRadius="3px" color="#fff" transition="300ms ease-in-out" _hover={{ opacity: "0.8" }} mr="1">{load3 ? <Spinner color="#fff" size="sm" emptyColor="lightgrey" /> : "Add Bank"}</Flex></button>
                                </form>
                            </Flex>
                        </Flex>

                        <Flex mt="8" w="100%" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3">
                            <Flex px="5" py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">My Bank  Details</Flex>
                            
                            <Box w={["260px", "100%"]} overflow={["auto", "hidden"]}>
                                <Flex py="5" px="5">
                                    <Flex flex="1" direction="column">
                                        <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold">Bank Name</Text>
                                        <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1">{userData[0]?.bankname}</Text>
                                    </Flex>
                                    
                                    <Flex flex="1" direction="column">
                                        <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold">Account Name</Text>
                                        <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1">{userData[0]?.accname}</Text>
                                    </Flex>
                                    
                                    <Flex flex="1" direction="column">
                                        <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold">Account Number</Text>
                                        <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1">{userData[0]?.accno}</Text>
                                    </Flex>
                                    
                                    <Flex flex="1" direction="column">
                                        <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold">Actions<Text display={["block", "none"]} color="transparent">lol</Text></Text>
                                        <Text bg="#e8e8e8" py="4" px="6" color="#495057" fontSize="14px" mb="1">{userData[0]?.accname !== "" ? "Bank Added" : ""}</Text>
                                    </Flex>
                                </Flex>
                            </Box>
                        </Flex>
                    </Flex>
                    :
                    tab === 2 ?
                    <Flex w="100%" direction="column">
                        <Flex w="100%" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3">
                            <Flex px="5" py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Add Crypto Wallet</Flex>
                            <Flex w="100%" py="5" px="5">
                                <form onSubmit={(e) => addWallet(e)} style={{ width: "100%" }}>
                                    <Flex w="100%" justify="space-between" direction={["column", "row"]}>
                                        <Flex w={["100%", "47%"]} direction="column" mb="5">
                                            <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Coin Name</Text>
                                            <input className="wInput" type="text" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem" }} placeholder="Exp. BTC, ETH, BCH, XRP" required id="coinname" />
                                        </Flex>

                                        <Flex w={["100%", "47%"]} direction="column" mb="5">
                                            <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Coin Host</Text>
                                            <input className="wInput" type="text" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem" }} placeholder="Exp. Blockchain, Coinbase, Paxful" required id="coinhost" />
                                        </Flex>
                                    </Flex>
                                    <Flex w="100%" direction="column" mb="5">
                                        <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Wallet</Text>
                                        <input className="wInput" type="text" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem" }} placeholder="Wallet Address" required id="wallet" />
                                    </Flex>
                                    <button type="submit" style={{ background: "transparent", border: "none" }}><Flex bg="#0A4FA8" cursor="pointer" padding="6px 15px" fontSize="14px" borderRadius="3px" color="#fff" transition="300ms ease-in-out" _hover={{ opacity: "0.8" }} mr="1">{load4 ? <Spinner color="#fff" size="sm" emptyColor="lightgrey" /> : "Add Wallet Address"}</Flex></button>
                                </form>
                            </Flex>
                        </Flex>

                        <Flex mt="8" w="100%" boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3">
                            <Flex px="5" py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">My Wallet Addresses</Flex>
                            
                            <Box w={["260px", "100%"]} overflow={["auto", "hidden"]}>
                                <Flex py="5" px="5">
                                    <Flex w={["auto", "20%"]} direction="column">
                                        <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold">Coin</Text>
                                        <Text py="4" px="6" color="#495057" fontSize="14px" mb="1">{userData[0]?.coinname}</Text>
                                    </Flex>
                                    
                                    <Flex w={["auto", "20%"]} direction="column">
                                        <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold">Coin Host</Text>
                                        <Text py="4" px="6" color="#495057" fontSize="14px" mb="1">{userData[0]?.coinhost}</Text>
                                    </Flex>
                                    
                                    <Flex w={["auto", "40%"]} direction="column">
                                        <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold">Wallet Address</Text>
                                        <Text py="4" px="6" color="#495057" fontSize="14px" mb="1">{userData[0]?.wallet}</Text>
                                    </Flex>
                                    
                                    <Flex w={["auto", "20%"]} direction="column">
                                        <Text color="#495057" py="4" px="6" fontSize="14px" mb="1" fontWeight="bold">Actions</Text>
                                        <Text py="4" px="6" color="#495057" overflowWrap="wrap" fontSize="14px" mb="1">{userData[0]?.coinname !== "" ? "Wallet Added" : ""}</Text>
                                    </Flex>
                                </Flex>
                            </Box>
                        </Flex>
                    </Flex>
                    :
                    tab === 3 ?
                    <>
                        {
                            userData[0]?.selfie === true ?
                            <Flex w="100%" justify="space-between" align="flex-start">
                                <Flex w={["100%", "47.7%"]} boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3">
                                    <Flex px="5" py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Verification status</Flex>
                                    <Flex pt="7" pb="6" px="8" direction="column" align="center">
                                        <Flex mt="-2" w="100%" bg="#fff" borderLeft="4px solid #ffad46" padding=".95rem 1.25rem" boxShadow="1px 1px 14px 0 rgb(18 38 63 / 26%)" fontSize="14px">Your verification is under review. You will be notified shortly</Flex>
                                    </Flex>
                                </Flex>
                            </Flex>
                            :
                            <form style={{ width: "100%" }} onSubmit={(e) => addKyc(e)}>
                                <Flex w="100%" justify="space-between" align="flex-start" direction={["column", "row"]}>
                                    <Flex w={["100%", "47.7%"]} boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3">
                                        <Flex px="5" py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">KYC Level Upgrade</Flex>
                                        <Flex py="5" px="8" direction="column" align="center">
                                            <Text textAlign="center" pb="4" color="#575962" borderBottom="1px solid #dee2e6" w="100%" fontSize="19px">Upload Selfie</Text>
                                            <Text mt="5" w="100%" color="#ffad46" textAlign="left" fontSize="14px">Take a selfie of yourself holding your ID with your full face clearly shown</Text>
                                            <Image h="250px" mt="7" mb="4" src={acc} />
                                            <input className="wInput" type="file" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem", width: "100%" }} accept="image/x-png,image/jpeg" required id="selfie" onChange={(e) => {
                                                setSelectedFile2(e.target.files[0])
                                            }} />
                                        </Flex>
                                        <Flex px="8" py="5" direction="column" borderTop="1px solid #dee2e6">
                                            <Text color="#575962" fontSize="19px">Identity verification</Text>
                                            <Text color="#575962" fontSize="14px" mt="2">Valid documents are: Country ID Card, Internattional Passport and Drivers Licence</Text>
                                            <Flex w="100%" direction="column" mt="7">
                                                <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Card Type</Text>
                                                <select className="wInput" type="text" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem", width: "100%" }} placeholder="Wallet Address" onChange={(e) => {
                                                    setIdShow(e.target.value)
                                                }}>
                                                    <option value="" disabled="">Select ID type</option>
                                                    <option value="ci">Country/State ID</option>
                                                    <option value="ii">International Passport</option>
                                                    <option value="di">Driver's License</option>
                                                </select>
                                                {
                                                    idShow === "" ?
                                                    <></>
                                                    :
                                                    <>
                                                        <Flex w="100%" direction="column" mt="5" pt="5" borderTop="1px solid #dee2e6">
                                                            <Text color="#495057" fontSize="14px" mb="3" fontWeight="bold">Card Front</Text>
                                                            <Image h={["auto", "120px"]} w={["100%", "200px"]} mb="3" src={front} />
                                                            <input className="wInput" type="file" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem", width: "100%" }} accept="image/x-png,image/jpeg" required id="cardfront" onChange={(e) => {
                                                                setSelectedFile3(e.target.files[0])
                                                            }} />
                                                        </Flex>
                                                        {
                                                            idShow !== "ii" &&
                                                            <Flex w="100%" direction="column" mt="7" pt="5" borderTop="1px solid #dee2e6">
                                                                <Text color="#495057" fontSize="14px" mb="3" fontWeight="bold">Card Back</Text>
                                                                <Image h={["auto", "120px"]} w={["100%", "200px"]} mb="3" src={back} />
                                                                <input className="wInput" type="file" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem", width: "100%" }} accept="image/x-png,image/jpeg" required id="cardback" onChange={(e) => {
                                                                    setSelectedFile4(e.target.files[0])
                                                                }} />
                                                            </Flex>
                                                        }
                                                    </>
                                                }
                                            </Flex>
                                        </Flex>
                                    </Flex>

                                    <Flex mt={["7", "0"]} w={["100%", "47.7%"]} boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3">
                                        <Flex px="5" py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Proof of Address</Flex>
                                        <Flex py="5" px="8" direction="column">
                                            <Text color="#575962" fontSize="14px" mb="5">Valid documents are: Utility bill and Bank statement</Text>
                                            <input className="wInput" type="file" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem", width: "100%" }} required id="proofaddress" onChange={(e) => {
                                                setSelectedFile5(e.target.files[0])
                                            }} />
                                            <Flex mt="12" w="100%">
                                                <button type="submit" style={{ background: "transparent", border: "none" }}><Flex bg="#0A4FA8" cursor="pointer" padding="6px 15px" fontSize="14px" borderRadius="3px" color="#fff" transition="300ms ease-in-out" _hover={{ opacity: "0.8" }} mr="1">{load5 ? <Spinner color="#fff" size="sm" emptyColor="lightgrey" /> : "Upload"}</Flex></button>
                                            </Flex>
                                        </Flex>
                                    </Flex>
                                </Flex>
                            </form>
                        }
                    </>
                    :
                    tab === 4 &&    
                    <Flex w="100%" justify="space-between" align="flex-start" direction={["column", "row"]}>
                        <Flex w={["100%", "47.7%"]} boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3">
                            <Flex px="5" py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">2FA Security</Flex>
                            {
                                showFa ?
                                <Flex py="5" px="8" direction="column">
                                    <Text color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6" pb="5" mb="5">QR CODE</Text>
                                    <Flex direction="column" mt="4" w="100%" align="center" color="#575962">
                                        <Image src={qr} w="200px" h="200px" />
                                        <Text fontSize="14px" textAlign="center">Scan the QR CODE with Google Authenticator and enter the coe dispayed in the box below.</Text>
                                        <form style={{ width: "100%" }} onSubmit={(e) => isfaFunc(e, "1")}>
                                            <Flex w="100%" align="center" direction="column" mt="5">
                                                <input className="wInput" type="number" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem" }} placeholder="6-digit OTP" required id="fa2" maxLength="6" />
                                                <Flex mt="4">
                                                    <button type="submit" style={{ background: "transparent", border: "none" }}><Flex bg="#0A4FA8" cursor="pointer" padding="6px 15px" fontSize="14px" borderRadius="3px" color="#fff" transition="300ms ease-in-out" _hover={{ opacity: "0.8" }} mr="1">{load7 ? <Spinner color="#fff" size="sm" emptyColor="lightgrey" /> : "Activate 2FA"}</Flex></button>
                                                </Flex>
                                            </Flex>
                                        </form>
                                    </Flex>
                                </Flex>
                                :
                                <Flex pb="5" pt="8" px="8" direction="column">     
                                    <Text color="#495057" fontSize="14px" mb="2" fontWeight="bold">Enable/Disable</Text>
                                    <Flex>
                                        <Flex padding="5px 15px" bg={userData[0]?.isfa === "1" ? "#31ce36" : "#999"} fontSize="14px" color={userData[0]?.isfa === "1" ? "#fff" : "#495057"} borderTopLeftRadius="3px" borderBottomLeftRadius="3px" cursor="pointer" _hover={{ opacity: "0.8" }} fontWeight="bold" transition="300ms ease-in-out" onClick={() => setShowFa(true)}> 
                                            Enable
                                        </Flex>
                                        <Flex padding="5px 15px" bg={userData[0]?.isfa === "0" ? "#31ce36" : "#999"} fontSize="14px" color={userData[0]?.isfa === "0" ? "#fff" : "#495057"} borderTopRightRadius="3px" borderBottomRightRadius="3px" cursor="pointer" _hover={{ opacity: "0.8" }} fontWeight="bold" transition="300ms ease-in-out" onClick={(e) => isfaFunc(e, "0")}> 
                                            {load8 ? <Spinner color="#fff" size="sm" emptyColor="lightgrey" /> : "Disable"}
                                        </Flex>
                                    </Flex>
                                    <Flex justify="flex-end" mt="1.5rem"><Text fontSize="13px" color="#575962">Current status: {userData[0]?.isfa === "1" ? "Enabled" : "Disabled"}</Text></Flex>
                                </Flex>
                            }
                        </Flex>

                        <Flex mt={["7", "0"]} w={["100%", "47.7%"]} boxShadow="2px 6px 15px 0 rgb(69 65 78 / 10%)" borderRadius="5px" direction="column" pb="3">
                            <Flex px="5" py="5" color="#575962" fontSize="20px" borderBottom="1px solid #dee2e6">Change Password</Flex>
                            
                            <Flex pb="5" pt="8" px="8" direction="column">
                                <form style={{ width: "100%" }} onSubmit={(e) => changePass(e)}>
                                    <Flex w="100%" direction="column" mb="5">
                                        <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Old Password</Text>
                                        <input className="wInput" type="password" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem" }} placeholder="Your current password" required id="opass" />
                                    </Flex>

                                    
                                    <Flex w="100%" direction="column" mb="5">
                                        <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">New Password</Text>
                                        <input className="wInput" type="password" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem" }} placeholder="New Password" required id="npass" />
                                    </Flex>

                                    
                                    <Flex w="100%" direction="column" mb="5">
                                        <Text color="#495057" fontSize="14px" mb="1" fontWeight="bold">Confirm Password</Text>
                                        <input className="wInput" type="password" style={{ background: "#fff", color: "#6c757d", borderRadius: "0.25rem", border: "1px solid #ced4da", padding: ".6rem 1rem" }} placeholder="Confirm Password" required id="cpass" />
                                    </Flex>
                                    <button type="submit" style={{ background: "transparent", border: "none" }}><Flex bg="#0A4FA8" cursor="pointer" padding="6px 15px" fontSize="14px" borderRadius="3px" color="#fff" transition="300ms ease-in-out" _hover={{ opacity: "0.8" }} mr="1">{load6 ? <Spinner color="#fff" size="sm" emptyColor="lightgrey" /> : "Upload Password"}</Flex></button>
                                </form>
                            </Flex>
                        </Flex>
                    </Flex>
                }
            </Flex>
        </Flex>
    )
}