import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { Flex, Text, Image } from "@chakra-ui/react";
import acc from "./acc.png";


export default function Sidebar({ minBar, selected, userData, isSidebar, setIsSidebar }) {
    const history = useHistory()
    const routes = [
        {
            tag: "Dashboard",
            link: "/dashboard",
            icon: "mdi mdi-layers",
        },
        {
            tag: "My Profile",
            link: "/dashboard/profile",
            icon: "mdi mdi-account",
        },
        {
            tag: "Wallet Deposit",
            link: "/dashboard/wallet",
            icon: "mdi mdi-wallet",
        },
        {
            tag: "Transfer Fund",
            link: "/dashboard/send_money",
            icon: "mdi mdi-send",
        },
        {
            tag: "Withdrawal",
            link: "/dashboard/withdrawal",
            icon: "mdi mdi-download",
        },
        {
            tag: "Downlines",
            link: "/dashboard/downlines",
            icon: "mdi mdi-account-multiple",
        },
        {
            tag: "Contact Support",
            link: "/dashboard/ticket",
            icon: "mdi mdi-headset",
        },
        {
            tag: "Logout",
            link: "/dashboard",
            icon: "mdi mdi-arrow-right",
        },
    ]

    return (
        <Flex w={[isSidebar ? "70%" : "0", minBar ? "75px" : "250px"]} px={[isSidebar ? "15px" : "0", minBar ? "2px" : "15px"]} direction="column" pt={["10px", "87px"]} position={["fixed", "sticky"]} bg="#fff" top="0" overflowY="scroll" h="100vh" zIndex={["1000", "10"]} boxShadow="4px 4px 10px rgb(69 65 78 / 6%)" transition={["width 200ms ease-out", "width 300ms ease-out"]} className="scPage">
            <Flex w="100%" display={["flex", "none"]} justify="flex-end" mb="3">
                <i className="mdi mdi-close" style={{ fontSize: "25px" }} onClick={() => isSidebar ? setIsSidebar(false) : setIsSidebar(true)}></i>
            </Flex>

            <Flex align="center" w="100%" pb="2" mb="5" borderBottom="1px solid rgba(0,0,0,0.04)" justify={minBar ? "center" : "flex-start"} px={minBar ? "2px" : "10px"} onClick={() => history.push("/dashboard/profile")} cursor="pointer">
                <Image h="2.5rem" src={userData[0]?.dp !== "" ? userData[0]?.dp : acc} borderRadius="100%" />
                {
                    !minBar &&
                    <Text fontSize="14px" ml="2">
                        <Text color="#777" textTransform="capitalize" w="160px" overflowWrap="break-word">{userData[0]?.uname ? userData[0]?.uname : ""}</Text>
                        <Text color="#555" fontSize="12px" fontWeight="bold" w="160px" overflowWrap="break-word">{userData[0]?.email ? userData[0]?.email : ""}</Text>
                    </Text>
                }
            </Flex>
            <Flex direction="column" w="100%">
                {
                    routes.map((item, index) => (
                        <Flex w="100%" align="center" key={index} justify={minBar ? "center" : "flex-start"} cursor="pointer" borderRadius="5px" bg={selected === index ? "rgba(0,0,0,0.05)" : "transparent" } onClick={() => {
                            if(index < 6) {
                                history.push(item.link)
                            }
                            else if(index === 6) {
                                if(window.tidioChatApi) {
                                    window.tidioChatApi.open()
                                }
                            }
                            else if(index === 7) {
                                sessionStorage.removeItem("worldIsLogged")
                                window.open("/login", "_SELF")
                            }
                            setIsSidebar(false)
                        }} py="5px" px={minBar ? "2px" : "10px"} color={selected === index ? "#575962" : "#8d9498"} fontWeight={selected === index ? "bold" : "normal"} _hover={{ color: "#575962", background: "rgba(0,0,0,0.05)", fontWeight: "bold" }} transition="200ms ease-in-out">
                            <i className={item.icon} style={{ fontSize: "25px" }}></i>
                            {
                                !minBar && <Text ml="20px" fontSize="14px">{item.tag}</Text>
                            }
                        </Flex>
                    ))
                }
            </Flex>
        </Flex>
    )
}