import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import Logo from "./logo.png";
import { Flex, Text, Image } from "@chakra-ui/react";
import acc from "./acc.png";


export default function NavbarDashboard({ minBar, setMinBar, userData, setIsModal, isSidebar, setIsSidebar }) {
    const [openMe, setOpenMe] = useState(false)
    const [notifications, setNotifications] = useState(false)
    const history = useHistory()
    
    const routes = [
        {
            tag: "Dashboard",
            link: "/dashboard",
            icon: "mdi mdi-desktop-mac",
        },
        {
            tag: "Deposit",
            link: "/dashboard/wallet",
            icon: "mdi mdi-folder",
        },
        {
            tag: "Transfer Fund",
            link: "/dashboard/send_money",
            icon: "mdi mdi-send",
        },
        {
            tag: "Withdrawal",
            link: "/dashboard/withdrawal",
            icon: "mdi mdi-download",
        },
        {
            tag: "Downlines",
            link: "/dashboard/downlines",
            icon: "mdi mdi-account-multiple",
        },
        {
            tag: "Contact Support",
            link: "/dashboard/ticket",
            icon: "mdi mdi-headset",
        },
    ]

    return (
        <Flex w="100%" bg="#000" position="fixed" boxShadow="0 0 5px rgb(18 23 39 / 50%)" align="center" justify="space-between" zIndex="1000" py="1" color="#fff">
            <Flex w={["auto", minBar ? "75px" : "250px"]} align="center" justify={minBar ? "center" : "space-between"} px={["4", minBar ? "5px" : "25px"]}>
                <Image h="60px" cursor="pointer" src={Logo} onClick={() => history.push("/dashboard")} mr={["2", "0"]} />

                <Flex display={["none", "flex"]} style={{ fontSize: "30px", cursor: "pointer", position: minBar ? "absolute" : "relative", marginLeft: minBar && "35px", zIndex: "100" }} onClick={() => minBar ? setMinBar(false) : setMinBar(true)}><i className={minBar ? "mdi mdi-dots-vertical" : "mdi mdi-menu"}></i>
                </Flex>

                <Flex display={["flex", "none"]} style={{ fontSize: "30px", cursor: "pointer", zIndex: "100" }} onClick={() => isSidebar ? setIsSidebar(false) : setIsSidebar(true)}><i className={minBar ? "mdi mdi-dots-vertical" : "mdi mdi-menu"}></i>
                </Flex>
            </Flex>

            <Flex align="center" px={["4", "30px"]}>
                <Flex direction="column" mr="3">
                    <Flex display={["none", "flex"]} align="center" mt="1" cursor="pointer" fontSize="14px" onClick={() => {
                        notifications ? setNotifications(false) : setNotifications(true)
                        setOpenMe(false)
                    }}>
                        <i className="mdi mdi-bell" style={{ fontSize: "18px" }}></i>
                        <Text mx="1">Notifications</Text>
                        <i className="mdi mdi-chevron-down" style={{ fontSize: "18px" }}></i>
                    </Flex> 
                    <Flex display={["flex", "none"]}><i className="mdi mdi-bell" style={{ fontSize: "25px" }} onClick={() => {
                        notifications ? setNotifications(false) : setNotifications(true)
                        setOpenMe(false)
                    }}></i></Flex>
                    {
                        notifications &&
                        <Flex w="260px" bg="#fff" color="#0000" position="absolute" right="0" mr={["62px", "92px"]} mt={["50px", "40px"]} data-aos="fade-left" boxShadow="0 1px 11px rgba(0,0,0,.15)" borderRadius="3px" direction="column" className="pop">
                            <Flex justify="center" align="center" py="4" mt="2" borderTop="1px solid rgba(0,0,0,0.04)">
                                <Text color="#1572e8" cursor="pointer" onClick={() => {
                                    history.push("/dashboard/notifications")
                                    setNotifications(false)
                                    setOpenMe(false)
                                    setIsModal(false)
                                }}>View all</Text>
                            </Flex>
                        </Flex>
                    }
                </Flex>
                <Flex direction="column">
                    <Image cursor="pointer" src={userData[0]?.dp !== "" ? userData[0]?.dp : acc} borderRadius="100%" h="40px" onClick={() => {
                        openMe ? setOpenMe(false) : setOpenMe(true)
                        setNotifications(false)
                    }} />
                    {
                        openMe &&
                        <Flex w="260px" bg="#fff" color="#0000" position="absolute" right="0" mr={["17px", "30px"]} mt="54px" data-aos="fade-left" boxShadow="0 1px 11px rgba(0,0,0,.15)" borderRadius="3px" direction="column" className="pop">
                            <Flex w="100%" py="3" px="5" borderBottom="1px solid rgba(0,0,0,0.04)" align="center">
                                <Image src={userData[0]?.dp !== "" ? userData[0]?.dp : acc} h="3.75rem" borderRadius="100%" />
                                <Text ml="3" onClick={() => {
                                    history.push("/dashboard/profile")
                                    setOpenMe(false)
                                    setNotifications(false)
                                    setIsModal(false)
                                }} cursor="pointer">
                                    <Text color="#1572e8" textTransform="capitalize" overflowWrap="break-word">Hi {userData[0]?.uname}</Text>
                                    <Text color="#6c757d" fontSize="12px" overflowWrap="break-word">{userData[0]?.email}</Text>
                                </Text>
                            </Flex>
                            <Flex w="100%" direction="column" py="3" px="0" borderBottom="1px solid rgba(0,0,0,0.04)">
                                {
                                    routes.map((item, index) => (
                                        <Flex py="1" px="7" key={index} _hover={{ bg: "rgba(0,0,0,0.05)" }} transition="200ms ease-in-out" cursor="pointer" color="#000" onClick={() => {
                                            if(index < 5) {
                                                history.push(item.link)
                                                setOpenMe(false)
                                                setNotifications(false)
                                                setIsModal(false)
                                            }
                                            else {
                                                setOpenMe(false)
                                                setNotifications(false)
                                                setIsModal(false)
                                                if(window.tidioChatApi) {
                                                    window.tidioChatApi.open()
                                                }
                                            }
                                        }}>
                                            <i className={item.icon} style={{ fontSize: "15px" }}></i>
                                            <Text fontSize="13px">&nbsp; {item.tag}</Text>
                                        </Flex>
                                    ))
                                }
                            </Flex>
                            <Flex py="1" mt="2" mb="2" px="7" _hover={{ bg: "rgba(0,0,0,0.05)" }} transition="200ms ease-in-out" cursor="pointer" color="#000" onClick={() => {
                                sessionStorage.removeItem("worldIsLogged")
                                window.open("/login", "_SELF")
                                setOpenMe(false)
                                setNotifications(false)
                                setIsModal(false)
                            }}>
                                <i className="mdi mdi-arrow-right" style={{ fontSize: "15px" }}></i>
                                <Text fontSize="13px">&nbsp; Logout</Text>
                            </Flex>
                        </Flex>
                    }
                </Flex>
            </Flex>
        </Flex>
    )
}