import { useState, useEffect } from "react";
import { Flex, Image, Text, Spinner, Box } from "@chakra-ui/react";
import { Fade } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css';
import img1 from "./img1.png";
import img2 from "./img2.png";
import img3 from "./img3.png";
import img4 from "./img4.png";
import img5 from "./img5.png";
import img6 from "./img6.png";
import img7 from "./img7.png";
import img8 from "./img8.png";
import img9 from "./img9.png";
import img11 from "./img11.png";
import img12 from "./img12.png";
import img14 from "./img14.png";
import icon1 from "./icon1.png";
import icon2 from "./icon2.png";
import icon3 from "./icon3.png";
import icon4 from "./icon4.png";
import icon5 from "./icon5.png";
import icon6 from "./icon6.png";
import icon7 from "./icon7.png";
import icon8 from "./icon8.png";
import icon9 from "./icon9.png";
import vid from "./vid.mp4";
import VideoCover from "./videoCover.png";
import Footer from "./footer";
import { useHistory } from "react-router-dom";
import mimg2 from "./mimg2.png";
import mimg3 from "./mimg3.png";
import mimg4 from "./mimg4.png";
import mimg5 from "./mimg5.png";
import mimg6 from "./mimg6.png";
import mimg7 from "./mimg7.png";
import mimg8 from "./mimg8.png";
import mimg9 from "./mimg9.png";
import mimg10 from "./mimg10.png";
import mimg11 from "./mimg11.png";
import mimg12 from "./mimg12.png";
import mimg13 from "./mimg13.png";
import mimg16 from "./mimg16.png";
import mimg17 from "./mimg17.png";
import mimg18 from "./mimg18.png";

function Home({ selected, setSelected }) {
	const [chart, setChart] = useState(img8)
	const [chart2, setChart2] = useState(mimg2)
	setSelected(0)
	const history = useHistory()
	useEffect(() => {
		document.title = "WORLDINVEST  TRADE  Cryptocurrency investment Company"
	}, [])
	const fadeImages = [
		{
			url: "bg1",
		},
		{
			url: "bg2",
		},
	];
	const properties = {
		duration: 3000,
		transitionDuration: 2000,
		infinite: true,
		autoplay: true,
		prevArrow: <Flex position="absolute" bg="rgba(0,0,0,0.5)" w="30px" h="110px" justify="center" align="center" cursor="pointer" ml={["5", "8"]}><i className="mdi mdi-chevron-left" style={{ fontSize: "30px" }}></i></Flex>,
		nextArrow:  <Flex position="absolute" right="0" bg="rgba(0,0,0,0.5)" w="30px" h="110px" justify="center" align="center" cursor="pointer" mr={["5", "8"]}><i className="mdi mdi-chevron-right" style={{ fontSize: "30px" }}></i></Flex>,
	};

	const howitworks = [
		{
			icon: icon1,
			title: "CREATE YOUR WALLET",
			sub: "Create an Account with us and Login to your Account.",
		},
		{
			icon: icon2,
			title: "ADD FUNDS TO YOUR WALLET",
			sub: "After you Login to your Dashboard, Deposit funds into your account and wait for Approval.",
		},
		{
			icon: icon3,
			title: "START EARNING",
			sub: "After approval, wait for profit top up. then start earning real time bitcoins.",
		},
	]

	const whyus = [
		{
			icon: icon4,
			title: "STRONG SECURITY",
			sub: "Protection against DDoS attacks, full data encryption",
		},
		{
			icon: icon5,
			title: "WORLD COVERAGE",
			sub: "Providing services in 99% countries around all the globe",
		},
		{
			icon: icon6,
			title: "PAYMENT OPTIONS",
			sub: "Popular methods: Visa, MasterCard, bank transfer, cryptocurrency",
		},
		{
			icon: icon7,
			title: "MOBILE TRADING",
			sub: "Trading via Mobile Phone on Our Platform",
		},
		{
			icon: icon8,
			title: "COST EFFICIENCY",
			sub: "Protection against DDoS attacks, full data encryption",
		},
		{
			icon: icon9,
			title: "HIGH LIQUIDITY",
			sub: "Fast access to high liquidity orderbook for top currency pairs",
		},
	]
	  
	return (
		<Flex w="100%" direction="column">
			<Box className="slide-container" minHeight={["40vh", "100vh"]} mt={["12", "0"]}>
				<Fade {...properties}>
					{fadeImages.map((item, index) => (
					<Flex direction="column" className="each-fade" key={index} minHeight={["40vh", "100vh"]}>
						<Flex w="100%" minHeight={["40vh", "100vh"]} align="center" justify="center" className={item.url} direction="column" pt="15%" pb="15%">
							<Flex direction="column" align="flex-start" justify="center">
								<Flex>
									<Flex w="16px" bg={index === 0 ? "#FE971A" : "transparent"} mr={["3", "5"]}></Flex>
									<Flex direction="column">
										<Text fontSize={["30px", "110px"]} fontWeight="bold" data-aos="fade-up" fontFamily="'Poppins', sans-serif">THEMOST <Text as="span" color="#FE971A">SECURE</Text></Text>
										<Text fontSize={["30px", "110px"]} mt={["0", "-10"]} fontWeight="bold" data-aos="fade-up" fontFamily="'Poppins', sans-serif"><Text as="span" color="#FE971A">{index === 0 ? "CRYPTO" : "TRADING"}</Text> {index === 0 ? "CURRENCY" : "STRATEGY"}</Text>
									</Flex>
								</Flex>
								<Text fontSize={["16px", "37px"]} ml={["8", "12"]} data-aos="fade-right">{index === 0 ? "Bitcoin is open source: It's design is public" : "Trade with Confidence"}</Text>
								<Flex data-aos="fade-up" mt={["20px", "80px"]} bg="rgb(254, 151, 26)" py={["3", "4"]} px={["8", "12"]} fontSize={["16px", "44px"]} borderRadius="25px" fontWeight="bold" cursor="pointer" transition="300ms ease-in-out" _hover={{ bg: "#1572e8" }} ml={["8", "0"]} onClick={() => history.push("/register")}>Get Started</Flex>
							</Flex>
						</Flex>
					</Flex>
					))}
				</Fade>
			</Box>


			<Flex w="100%" justify="center" pt="8" pb="6" color="#9DB2A6" fontSize="13px">
				<Text cursor="pointer" color="#1572e8" onClick={() => window.open("https://www.tradingview.com/", "_BLANK")} mr="2">Ticker Tape</Text>
				by TradingView
			</Flex>


			<Flex bg="#FE971A" px={["5", "12"]} direction="column" w="100%" py="6" color="#000">
				<Text textAlign="center" fontWeight="bold" fontSize={["28px", "2.375em"]} textTransform="uppercase">Easiest &amp; Most Powerful Assets</Text>
				<Flex justify="center"><Flex w="5%" h="1px" bg={["#000", "#BBB"]} mt="4" mb={["8", "12"]}></Flex></Flex>
				<Flex justify="space-between" direction={["column", "row"]}>
					<Flex w={["100%", "48%"]}>
						<Image src={img1} w="100%" />
					</Flex>
					<Flex w={["100%", "49%"]} direction="column" align="center">
						<Text mb="12" textAlign="center" fontSize="23px">Explore an ever-ending variety of cryptocurrencies, and buy and invest the underlying asset on Bayya Trade Cryptocurrency Investment Platform. Build your crypto-based portfolio and enjoy benefits not offered by most exchanges, such as near-immediate execution of market orders.</Text>
						<Flex bg="#000" color="#fff" padding="20px 35px" cursor="pointer" fontWeight="bold" fontSize="0.8125em" borderRadius="5px" transition="300ms ease-in-out" _hover={{ bg: "rgb(60,60,60)" }} onClick={() => history.push("/register")}>CREATE ACCOUNT</Flex>
					</Flex>
				</Flex>
			</Flex>


			<Flex w="100%"><Image w="100%" src={img2} display={["none", "block"]} /><Image w="100%" src={mimg16} display={["block", "none"]} /></Flex>


			<Flex px={["2", "12"]} justify="space-between" mt="3" direction={["column", "row"]}>
				{
					howitworks.map((item, index) => (
						<Flex data-aos="fade-up" key={index} direction="column" w={["100%", "31.5%"]} bg="#1E1E1E" py={["7", "5"]} px={["8", "12"]} align="center" mb={["5", "0"]}>
							<Image position="absolute" src={item.icon} w="50px" _hover={{ w: "60px" }} transition="300ms ease-in-out" />
							<Text fontSize="19px" mt="70px" mb="15px" fontWeight="bold" textAlign="center">{item.title}</Text>
							<Text textAlign="center" fontSize="12.6px">{item.sub}</Text>
						</Flex>
					))
				}
			</Flex>


			<Flex w="100%" mt="12"><Image w="100%" src={img3} display={["none", "block"]} /><Image w="100%" src={mimg17} display={["block", "none"]} mt="-8" /></Flex>


			<Flex bg="#000" px={["5", "12"]} direction="column" w="100%" pt={["3", "6"]} pb="6">
				<Flex justify="space-between" direction={["column", "row"]}>
					<Flex w={["100%", "48%"]}>
						<Image src={img11} w="100%" data-aos="fade-right" />
					</Flex>
					<Flex w={["100%", "49%"]} direction="column" mt={["5", "0"]}>
						<Text fontWeight="bold" fontSize="25px">WE ARE WORLDINVEST</Text>
						<Text mb="8" color="#636363">A place for everyone who wants to simply trade Cryptocurrencies. Deposit funds using Cryptocurrency or bank transfer. Instant buying/selling of Cryptocurrencies at fair price is guaranteed. Nothing extra. Join over 700,000 users from all over the world satisfied with our services.</Text>
						<Flex><Flex bg="#FE971A" color="#fff" padding="12px 24px" cursor="pointer" fontWeight="bold" fontSize="15px" borderRadius="5px" transition="300ms ease-in-out" _hover={{ bg: "rgb(60,60,60)" }} onClick={() => history.push("/about-us")}>Read More</Flex></Flex>
					</Flex>
				</Flex>
			</Flex>


			<Flex w="100%"><Image w="100%" src={img14} display={["none", "block"]} /><Image w="100%" src={mimg18} display={["block", "none"]} /></Flex>


			<Flex w="100%" px={["0", "12"]} justify="space-between" mt="6" direction={["column", "row"]}>
				<Flex w={["100%", "57%"]} justify="space-between" flexWrap="wrap" px={["5", "0"]}>
					{
						whyus.map((item, index) => (
							<Flex data-aos="fade-up" key={index} direction="column" w={["100%", "48.7%"]} bg="#1E1E1E" py="5" px="12" align="center" mb="5">
								<Image position="absolute" src={item.icon} h="50px" _hover={{ h: "60px" }} transition="300ms ease-in-out" />
								<Text fontSize="19px" mt="70px" mb="10px" fontWeight="bold">{item.title}</Text>
								<Text textAlign="center" fontSize="12.6px">{item.sub}</Text>
							</Flex>
						))
					}
				</Flex>
				<Flex w={["100%", "41%"]} className="bg3" align="flex-start" px={["2", "0"]}>
					<Flex h="100%" bg="rgba(0,0,0,0.7)" px={["0", "2"]} pt="2" pb={["4", "0"]} align="flex-start" w="100%">
						<video src={vid} style={{ width: "100%" }} controls playsInline poster={VideoCover} type="video/mp4"></video>
					</Flex>
				</Flex>
			</Flex>

			
			<Flex w="100%" display={["none", "flex"]}><Image w="100%" src={img4} /></Flex>
			<Flex w="100%" display={["flex", "none"]} direction="column"><Image w="100%" src={mimg13} /><Image w="100%" src={mimg12} mt="-2" /></Flex>

			
			<Flex w="100%" px={["2", "0"]}><Image w="100%" src={img5} display={["none", "block"]} /><Image w="100%" src={mimg11} display={["block", "none"]} mt="4" /></Flex>

			
			<Flex w="100%" px="12" display={["none", "flex"]}><Image w="100%" src={img12} onClick={() => history.push("/register")} /></Flex>
			
			<Flex w="100%" px="3" mt="5" display={["flex", "none"]} direction="column">
				<Image w="100%" src={mimg10} mb="4" onClick={() => history.push("/register")} />
				<Image w="100%" src={mimg9} mb="4" onClick={() => history.push("/register")} />
				<Image w="100%" src={mimg8} mb="4" onClick={() => history.push("/register")} />
				<Image w="100%" src={mimg7} mb="4" onClick={() => history.push("/register")} />
			</Flex>

			
			<Flex w="100%" px="4" display={["none", "flex"]}><Image w="100%" src={img6} /></Flex>
			<Flex w="100%" px="3" mt="5" display={["flex", "none"]} direction="column">
				<Image w="100%" src={mimg6} mb="4" data-aos="fade-up" />
				<Image w="100%" src={mimg5} mb="4" data-aos="fade-up" />
			</Flex>
			
			<Flex w="100%" px={["0", "12"]} mt="2"><Image w="100%" src={img7} display={["none", "block"]} /><Image w="100%" src={mimg4} display={["block", "none"]} /></Flex>

			
			<Flex w="100%" px="12" mt="2" display={["none", "flex"]}><Flex px="12"><Image w="100%" src={chart} cursor="crosshair" onMouseEnter={() => setChart(img9)} onMouseLeave={() => setChart(img8)} /></Flex></Flex>

			<Flex w="100%" mt="3" display={["flex", "none"]} px="3"><Flex><Image w="100%" src={chart2} cursor="crosshair" onMouseEnter={() => setChart2(mimg3)} onMouseLeave={() => setChart2(mimg2)} /></Flex></Flex>

			<Flex w="100%" justify="center" pt="2" pb="5" color="#9DB2A6" fontSize="13px">
				<Text cursor="pointer" color="#1572e8" onClick={() => window.open("https://www.tradingview.com/symbols/BTCUSD/?exchange=COINBASE", "_BLANK")} mr="2">BTCUSD Chart</Text>
				by TradingView
			</Flex>

			
			<Footer selected={selected} setSelected={setSelected} />
		</Flex>
	)
}


export default Home;