import { Flex } from "@chakra-ui/react";
import { useHistory } from "react-router-dom";


export default function DepBtn() {
    const history = useHistory()
    return(
        <Flex cursor="pointer" bg="#6861ce" color="#fff" padding="5px 15px" borderRadius="100px" fontSize="14px" transition="300ms ease-in-out" _hover={{ opacity: "0.9" }} onClick={() => {
            /*if(window.tidioChatApi) {
                window.tidioChatApi.open()
            }*/
            history.push("/dashboard/wallet")
        }}>Deposit</Flex>
    )
}